<template>
  <div id="control">
    <div class="box">
      <ul>
        <li :style="{backgroundColor:bgColor[0],color:color[0]}" @click="changeBgColor(0)">环卫人员</li>
        <li :style="{backgroundColor:bgColor[1],color:color[1]}" @click="changeBgColor(1)">环卫车</li>
        <li :style="{backgroundColor:bgColor[2],color:color[2]}" @click="changeBgColor(2)">环卫机器人</li>
        <li :style="{backgroundColor:bgColor[3],color:color[3]}" @click="changeBgColor(3)">智能垃圾桶</li>
        <li :style="{backgroundColor:bgColor[4],color:color[4]}" @click="changeBgColor(4)">道路积水监测</li>
      </ul>
    </div>
    <div id="controlNav">
      <!-- 环卫工 -->
      <div class="displayList" v-if="showList[0]">
        <div class="row" style="margin:0px;">
          <div class="col-lg-12">
            <div class="input-group">
              <input type="text" class="form-control" placeholder="请输入设备的IMEI" v-model="searchText">
              <span class="input-group-btn">
                <button class="btn btn-default" type="button" @click="search">
                  <span class="glyphicon glyphicon-search" aria-hidden="true"></span>
                </button>
              </span>
            </div>
          </div>
        </div>
        <!-- 查询 -->
        <div v-if="noSearch" style="text-align:center;padding:5%;">暂无数据</div>
        <ul v-if="searchStatus">
          <li v-for="search in searches" :key="search.id">
            <div class="msg">
              <div style="height:100%;width:37px;float:left;padding:5px;margin:0px 10px 0px 10px;">
                <img src="../assets/worker2.png" alt="car" height="100%">
              </div>
              <div style="height:100%;float:left">
                <div>IMEI：{{search.imei}}</div>
                <div>用户姓名：{{search.username}}</div>
              </div>
            </div>
            <div class="operate">
              <div class="cell" style="border-right:1px solid #dbdcdc">
                <div>
                  <input type="checkbox" :value="search.imei" v-model="imeis">
                  <span>显示定位</span>
                </div>
              </div>
              <div class="cell" @click="TrackImei(search.imei,search.id)">
                <router-link tag="div" :to="{name:'Track'}">轨迹回放</router-link>
              </div>
            </div>
          </li>
        </ul>
        <!-- 查询结果 -->
        <ul v-if="deviceStatus">
          <li
            v-for="device in showWorkers"
            :key="device.id"
            :style="{backgroundColor:liBgColor[device.id]}"
          >
            <div class="msg">
              <div style="height:100%;width:37px;float:left;padding:5px;margin:0px 10px 0px 10px;">
                <img src="../assets/worker2.png" alt="car" height="100%">
              </div>
              <div style="height:100%;float:left">
                <div>IMEI：{{device.imei}}</div>
                <div>用户姓名：{{device.username}}</div>
              </div>
            </div>
            <div class="operate">
              <div class="cell" style="border-right:1px solid #dbdcdc">
                <div>
                  <input type="checkbox" :value="device.imei" v-model="imeis">
                  <span>显示定位</span>
                </div>
              </div>
              <div class="cell" @click="TrackImei(device.imei,device.id)">
                <router-link tag="div" :to="{name:'Track'}">轨迹回放</router-link>
              </div>
            </div>
          </li>
        </ul>
        <!-- 显示环卫工信息 -->
        <nav aria-label="Page navigation" v-if="deviceStatus">
          <ul class="pagination pagination-sm" style="padding-left:70px;">
            <li style="cursor: pointer;" @click="previousWorker">
              <a
                style="color:white !important;background-color:#283966;borderColor:#283966;margin-right:5px;"
              >上一页</a>
            </li>
            <li>
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >{{n}}/{{m}}</a>
            </li>
            <li style="cursor: pointer;" @click="nextWorker">
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >下一页</a>
            </li>
          </ul>
        </nav>
      </div>
      <!-- 环卫车 -->
      <div class="displayList" v-if="showList[1]">
        <div class="row" style="margin:0px;">
          <div class="col-lg-12">
            <div class="input-group">
              <input
                type="text"
                class="form-control"
                placeholder="请输入设备的IMEI"
                v-model="searchCarText"
              >
              <span class="input-group-btn">
                <button class="btn btn-default" type="button" @click="searchingCar">
                  <span class="glyphicon glyphicon-search" aria-hidden="true"></span>
                </button>
              </span>
            </div>
          </div>
        </div>
        <!-- 查询 -->
        <div v-if="noSearchCar" style="text-align:center;padding:5%;">暂无数据</div>
        <ul v-if="searchCar">
          <li v-for="car in searchCarResult" :key="car.id">
            <div class="msg">
              <div
                style="height:100%;width:50px;float:left;padding-top:10px;margin:0px 10px 0px 10px;"
              >
                <img src="../assets/car1.png" alt="car" height="70%">
              </div>
              <div style="height:100%;float:left">
                <div>IMEI：{{car.imei}}</div>
                <div>车牌号：{{car.sim}}</div>
              </div>
            </div>
            <div class="operate">
              <div class="cell" style="border-right:1px solid #dbdcdc">
                <div>
                  <input type="checkbox" :value="car.imei" v-model="carimeis">
                  <span>显示定位</span>
                </div>
              </div>
              <div class="cell" @click="showCarTrack(car.imei)">
                <router-link tag="div" :to="{name:'Track'}">轨迹回放</router-link>
              </div>
            </div>
          </li>
        </ul>
        <!-- 查询结果 -->
        <ul v-if="displayCar">
          <li v-for="car in showCars" :key="car.id">
            <div class="msg">
              <div
                style="height:100%;width:50px;float:left;padding-top:10px;margin:0px 10px 0px 10px;"
              >
                <img src="../assets/car1.png" alt="car" height="70%">
              </div>
              <div style="height:100%;float:left">
                <div>IMEI：{{car.imei}}</div>
                <div>车牌号：{{car.sim}}</div>
              </div>
            </div>
            <div class="operate">
              <div class="cell" style="border-right:1px solid #dbdcdc">
                <div>
                  <input type="checkbox" :value="car.imei" v-model="carimeis">
                  <span>显示定位</span>
                </div>
              </div>
              <div class="cell" @click="showCarTrack(car.imei)">
                <router-link tag="div" :to="{name:'Track'}">轨迹回放</router-link>
              </div>
            </div>
          </li>
        </ul>
        <!-- 显示环卫车信息 -->
        <nav aria-label="Page navigation" v-if="displayCar">
          <ul class="pagination pagination-sm" style="padding-left:70px">
            <li style="cursor: pointer;" @click="previousCar">
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >上一页</a>
            </li>
            <li>
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >{{carN}}/{{carM}}</a>
            </li>
            <li style="cursor: pointer;" @click="nextCar">
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >下一页</a>
            </li>
          </ul>
        </nav>
      </div>
      <!-- 环卫机器人 -->
      <div class="displayList" v-if="showList[2]">
        <div class="row" style="margin:0px;">
          <div class="col-lg-12">
            <div class="input-group">
              <input
                type="text"
                class="form-control"
                placeholder="请输入设备的IMEI"
                v-model="searchRobotText"
              >
              <span class="input-group-btn">
                <button class="btn btn-default" type="button" @click="searchingRobot">
                  <span class="glyphicon glyphicon-search" aria-hidden="true"></span>
                </button>
              </span>
            </div>
          </div>
        </div>
        <!-- 查询 -->
        <div v-if="noSearchRobot" style="text-align:center;padding:5%;">暂无数据</div>
        <ul v-if="searchRobot">
          <li v-for="robot in searchRobotResult" :key="robot.id">
            <div class="msg">
              <div
                style="height:100%;width:45px;float:left;padding:5px 0px;margin:0px 10px 0px 10px;"
              >
                <img src="../assets/robot2.png" alt="car" height="100%">
              </div>
              <div style="height:100%;float:left">
                <div>IMEI：{{robot.imei}}</div>
                <div>设备名称：{{robot.deviceName}}</div>
              </div>
            </div>
            <div class="operate">
              <div class="cell" style="border-right:1px solid #dbdcdc">
                <div>
                  <input type="checkbox" :value="robot.imei" v-model="robotimeis">
                  <span>显示定位</span>
                </div>
              </div>
              <div class="cell" @click="showRobotTrack(robot.imei)">
                <router-link tag="div" :to="{name:'Track'}">轨迹回放</router-link>
              </div>
            </div>
          </li>
        </ul>
        <!-- 查询结果 -->
        <ul v-if="displayRobot">
          <li v-for="robot in showRobots" :key="robot.id">
            <div class="msg">
              <div
                style="height:100%;width:45px;float:left;padding:5px 0px;margin:0px 10px 0px 10px;"
              >
                <img src="../assets/robot2.png" alt="car" height="100%">
              </div>
              <div style="height:100%;float:left">
                <div>IMEI：{{robot.imei}}</div>
                <div>设备名称：{{robot.deviceName}}</div>
              </div>
            </div>
            <div class="operate">
              <div class="cell" style="border-right:1px solid #dbdcdc">
                <div>
                  <input type="checkbox" :value="robot.imei" v-model="robotimeis">
                  <span>显示定位</span>
                </div>
              </div>
              <div class="cell" @click="showRobotTrack(robot.imei)">
                <router-link tag="div" :to="{name:'Track'}">轨迹回放</router-link>
              </div>
            </div>
          </li>
        </ul>
        <!-- 显示环卫机器人信息 -->
        <nav aria-label="Page navigation" v-if="displayRobot">
          <ul class="pagination pagination-sm" style="padding-left:70px">
            <li style="cursor: pointer;" @click="previousRobot">
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >上一页</a>
            </li>
            <li>
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >{{robotN}}/{{robotM}}</a>
            </li>
            <li style="cursor: pointer;" @click="nextRobot">
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >下一页</a>
            </li>
          </ul>
        </nav>
      </div>
      <!-- 智能垃圾桶 -->
      <div class="displayList" v-if="showList[3]">
        <div class="row" style="margin:0px;">
          <div class="col-lg-12">
            <div class="input-group">
              <input
                type="text"
                class="form-control"
                placeholder="请输入设备的IMEI"
                v-model="searchTrashText"
              >
              <span class="input-group-btn">
                <button class="btn btn-default" type="button" @click="searchingTrash">
                  <span class="glyphicon glyphicon-search" aria-hidden="true"></span>
                </button>
              </span>
            </div>
          </div>
        </div>
        <!-- 查询 -->
        <div v-if="noSearchTrash" style="text-align:center;padding:5%;">暂无数据</div>
        <ul v-if="searchTrash">
          <li v-for="can in searchTrashResult" :key="can.id" class="trash">
            <div class="msg">
              <div style="height:100%;float:left;width:45px;padding-top:6px;margin:0px 10px 0px 10px;">
                <input type="checkbox" style="margin-right:5px;" :value="can" v-model="sendtrash">
                <img src="../assets/trash.png" alt="trash" height="80%">
              </div>
              <div style="height:100%;float:left">
                <div>IMEI：{{can.imei}}</div>
                <div>
                  <img
                    src="../assets/signal.png"
                    alt="signal"
                    height="10"
                    style="margin:0px 8px 0px 8px;"
                  >
                  <img
                    src="../assets/power5.png"
                    alt="power"
                    height="10"
                    style="margin:0px 3px 0px 0px;"
                    v-if="can.electric_quantity >99"
                  >
                  <img
                    src="../assets/power4.png"
                    alt="power"
                    height="10"
                    style="margin:0px 3px 0px 0px;"
                    v-else-if="can.electric_quantity >75"
                  >
                  <img
                    src="../assets/power3.png"
                    alt="power"
                    height="10"
                    style="margin:0px 3px 0px 0px;"
                    v-else-if="can.electric_quantity >50"
                  >
                  <img
                    src="../assets/power2.png"
                    alt="power"
                    height="10"
                    style="margin:0px 3px 0px 0px;"
                    v-else-if="can.electric_quantity >25"
                  >
                  <img
                    src="../assets/power1.png"
                    alt="power"
                    height="10"
                    style="margin:0px 3px 0px 0px;"
                    v-else
                  >
                  {{can.electric_quantity}}%
                  <span
                    style="margin-left:20px;"
                  >{{can.trashstatus}}</span>
                </div>
              </div>
            </div>
          </li>
        </ul>
        <!-- 查询结果 -->
        <ul v-if="displayTrash">
          <li v-for="can in showTrash" :key="can.id" class="trash">
            <div class="msg">
              <div style="height:100%;float:left;width:45px;padding-top:6px;margin:0px 10px 0px 10px;">
                <input type="checkbox" style="margin-right:5px;" :value="can" v-model="sendtrash">
                <img src="../assets/trash.png" alt="trash" height="80%">
              </div>
              <div style="height:100%;float:left">
                <div>IMEI：{{can.imei}}</div>
                <div>
                  <img
                    src="../assets/signal.png"
                    alt="signal"
                    height="10"
                    style="margin:0px 8px 0px 8px;"
                  >
                  <img
                    src="../assets/power5.png"
                    alt="power"
                    height="10"
                    style="margin:0px 3px 0px 0px;"
                    v-if="can.electric_quantity >99"
                  >
                  <img
                    src="../assets/power4.png"
                    alt="power"
                    height="10"
                    style="margin:0px 3px 0px 0px;"
                    v-else-if="can.electric_quantity >75"
                  >
                  <img
                    src="../assets/power3.png"
                    alt="power"
                    height="10"
                    style="margin:0px 3px 0px 0px;"
                    v-else-if="can.electric_quantity >50"
                  >
                  <img
                    src="../assets/power2.png"
                    alt="power"
                    height="10"
                    style="margin:0px 3px 0px 0px;"
                    v-else-if="can.electric_quantity >25"
                  >
                  <img
                    src="../assets/power1.png"
                    alt="power"
                    height="10"
                    style="margin:0px 3px 0px 0px;"
                    v-else
                  >
                  {{can.electric_quantity}}%
                  <span
                    style="margin-left:20px;"
                  >{{can.trashstatus}}</span>
                </div>
              </div>
            </div>
          </li>
        </ul>
        <!-- 显示智能垃圾桶信息 -->
        <nav aria-label="Page navigation" v-if="displayTrash">
          <ul class="pagination pagination-sm" style="padding-left:70px">
            <li style="cursor: pointer;" @click="previousTrash">
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >上一页</a>
            </li>
            <li>
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >{{trashN}}/{{trashM}}</a>
            </li>
            <li style="cursor: pointer;" @click="nextTrash">
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >下一页</a>
            </li>
          </ul>
        </nav>
      </div>
      <!-- 道路积水监测 -->
      <div class="displayList" v-if="showList[4]">
        <div class="row" style="margin:0px;">
          <div class="col-lg-12">
            <div class="input-group">
              <input
                type="text"
                class="form-control"
                placeholder="请输入设备的IMEI"
                v-model="searchCameraText"
              >
              <span class="input-group-btn">
                <button class="btn btn-default" type="button" @click="searchingCamera">
                  <span class="glyphicon glyphicon-search" aria-hidden="true"></span>
                </button>
              </span>
            </div>
          </div>
        </div>
        <!-- 查询 -->
        <div v-if="noSearchCamera" style="text-align:center;padding:5%;">暂无数据</div>
        <ul v-if="searchCamera">
          <li v-for="camera in searchCameraResult" :key="camera.id" class="camera">
            <div class="msg">
              <div
                style="height:100%;width:64px;float:left;padding-top:6px;margin:0px 10px 0px 10px;"
              >
                <input
                  type="checkbox"
                  style="margin-right:5px;"
                  :value="camera"
                  v-model="sendCamera"
                >
                <img src="../assets/camera.png" alt="camera" height="60%">
              </div>
              <div style="height:100%;float:left">
                <div>IMEI：{{camera.imei}}</div>
                <div>
                  <div>道路：{{camera.position}}</div>
                  <div>积水深度:{{camera.ponding_thickness}}cm</div>
                </div>
              </div>
            </div>
          </li>
        </ul>
        <!-- 查询结果 -->
        <ul v-if="displayCamera">
          <li v-for="camera in showCamera" :key="camera.id" class="camera">
            <div class="msg">
              <div
                style="height:100%;width:64px;float:left;padding-top:6px;margin:0px 10px 0px 10px;"
              >
                <input
                  type="checkbox"
                  style="margin-right:5px;"
                  :value="camera"
                  v-model="sendCamera"
                >
                <img src="../assets/camera.png" alt="camera" height="60%">
              </div>
              <div style="height:100%;float:left;">
                <div>IMEI：{{camera.imei}}</div>
                <div>
                  <div>道路：{{camera.position}}</div>
                  <div>积水深度:{{camera.ponding_thickness}}cm</div>
                </div>
              </div>
            </div>
          </li>
        </ul>
        <!-- 显示道路积水监测信息 -->
        <nav aria-label="Page navigation" v-if="displayCamera">
          <ul class="pagination pagination-sm" style="padding-left:70px">
            <li style="cursor: pointer;" @click="previousCamera">
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >上一页</a>
            </li>
            <li>
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >{{cameraN}}/{{cameraM}}</a>
            </li>
            <li style="cursor: pointer;" @click="nextCamera">
              <a
                style="color:white !important;backgroundColor:#283966;borderColor:#283966;margin-right:5px;"
              >下一页</a>
            </li>
          </ul>
        </nav>
      </div>
    </div>
    <div id="mapContainer">
      <router-view></router-view>
    </div>
  </div>
</template>
<script>
export default {
  name: "control",
  data() {
    return {
      map: null,
      bgColor: ["#1c2a4f", "", "", "", ""],
      color: ["#fd8403", "white", "white", "white", "white"],
      showList: [true, false, false, false, false],
      liBgColor: [],
      //环卫工
      devices: [],
      imeis: [],
      show: [],
      deviceStatus: true,
      searchStatus: false,
      noSearch: false,
      searchText: "",
      searches: [],
      showWorkers: [], //当前页展示的环卫工内容
      n: 1,
      m: 1,
      //环卫车
      displayCar: true,
      noSearchCar: false,
      searchCar: false,
      searchCarText: "",
      cars: [],
      searchCarResult: [],
      carimeis: [],
      showCars: [], //当前页展示的环卫车内容
      carN: 1,
      carM: 1,
      // 环卫机器人
      displayRobot: true,
      noSearchRobot: false,
      searchRobot: false,
      searchRobotText: "",
      robots: [],
      searchRobotResult: [],
      robotimeis: [],
      showRobots: [], //当前页展示的机器人内容
      robotN: 1,
      robotM: 1,
      //智能垃圾桶
      displayTrash: true,
      searchTrash: false,
      noSearchTrash: false,
      searchTrashText: "",
      trash: [],
      searchTrashResult: [],
      sendtrash: [],
      showTrash: [], //当前页展示的垃圾桶内容
      trashN: 1,
      trashM: 1,
      //道路积水监测
      displayCamera: true,
      searchCamera: false,
      noSearchCamera: false,
      searchCameraText: "",
      waterCameras: [],
      searchCameraResult: [],
      sendCamera: [],
      showCamera: [], //当前页展示的积水监测设备内容
      cameraN: 1,
      cameraM: 1
    };
  },
  computed: {
    // trash(){
    //     return this.$store.state.trash;
    // }
  },
  watch: {
    //环卫工
    imeis(newValue) {
      this.$store.commit("LocationImei", newValue);
      if (newValue.length) {
        this.$router.push({ name: "Location" });
      }
    },
    searchText(newValue) {
      if (newValue == "") {
        this.deviceStatus = true;
        this.searchStatus = false;
        this.noSearch = false;
      }
    },
    //环卫车
    searchCarText(newValue) {
      if (newValue == "") {
        (this.displayCar = true),
          (this.noSearchCar = false),
          (this.searchCar = false);
      }
    },
    carimeis(newValue) {
      this.$store.commit("locationCar", newValue);
      if (newValue.length) {
        this.$router.push({ name: "Location" });
      }
    },
    //机器人
    searchRobotText(newValue) {
      if (newValue == "") {
        this.displayRobot = true;
        this.noSearchRobot = false;
        this.searchRobot = false;
      }
    },
    robotimeis(newValue) {
      this.$store.commit("locationRobot", newValue);
      if (newValue.length) {
        this.$router.push({ name: "Location" });
      }
    },
    //智能垃圾桶
    searchTrashText(newValue) {
      if (newValue == "") {
        this.displayTrash = true;
        this.searchTrash = false;
        this.noSearchTrash = false;
      }
    },
    sendtrash(newValue) {
      this.$store.commit("locationTrash", newValue);
    },
    //积水监测
    searchCameraText(newValue) {
      if (newValue == "") {
        this.displayCamera = true;
        this.searchCamera = false;
        this.noSearchCamera = false;
      }
    },
    sendCamera(newValue) {
      this.$store.commit("locationCamera", newValue);
    }
  },
  mounted() {
    this.getWorkers();
    this.getCars();
    this.getRobots();
    this.getTrash();
    this.getWaterCamera();
    //未刷新情况下返回控制台页面自动获取已经选中的状态
    this.imeis = this.$store.state.workers.locationImeis;
    this.carimeis = this.$store.state.cars.locationImeis;
    this.robotimeis = this.$store.state.robots.locationImeis;
    this.sendtrash = this.$store.state.sendtrash;
    this.sendCamera = this.$store.state.sendCamera;
  },
  methods: {
    changeBgColor(n) {
      (this.bgColor = []), (this.bgColor[n] = "#1c2a4f");
      this.color = ["white", "white", "white", "white", "white"];
      this.color[n] = "#fd8403";
      this.showList = [false, false, false, false, false];
      this.showList[n] = true;
      this.$router.push({ name: "Location" });
    },
    //获取所有环卫工设备信
    getWorkers() {
      this.$Axios
        .get(this.$url + "/human/getAllDeviceInfo.do",{params:{
            t: Date.parse(new Date())
        }})
        .then(response => {
          if (response.data.status) {
            this.devices = response.data.list;
            this.showWorkers = this.devices.filter((elem, index, arr) => {
              return index < 4;
            });
            this.m = Math.ceil(this.devices.length / 4);
          }
        })
        .catch(error => {
          if (window.console) {
            console.log(error);
          }
        });
    },
    previousWorker() {
      if (this.n <= 1) {
        this.n = 1;
      } else {
        --this.n;
        this.showWorkers = this.devices.filter((elem, index, arr) => {
          return 4 * (this.n - 1) <= index && index <= 4 * this.n - 1;
        });
      }
    },
    nextWorker() {
      if (this.n >= Math.ceil(this.devices.length / 4)) {
        this.n = Math.ceil(this.devices.length / 4);
      } else {
        ++this.n;
        this.showWorkers = this.devices.filter((elem, index, arr) => {
          return 4 * (this.n - 1) <= index && index <= 4 * this.n - 1;
        });
      }
    },
    //查询环卫工
    search() {
      this.searches = [];
      this.noSearch = false;
      if (this.searchText) {
        for (let i = 0; i < this.devices.length; i++) {
          if (this.devices[i].imei == this.searchText) {
            this.searches.push(this.devices[i]);
          }
        }
        if (this.searches.length) {
          this.searchStatus = true;
          this.deviceStatus = false;
        } else {
          this.noSearch = true;
          this.deviceStatus = false;
        }
      }
    },
    //环卫工轨迹回放
    TrackImei(deviceImei, deviceId) {
      this.$store.commit("trackShowImei", deviceImei);
      this.$store.commit("trackType", "worker"), (this.liBgColor = []);
      // this.liBgColor[deviceId]="#fd8403";
      this.imeis = [];
    },
    //获取所有环卫车设备信
    getCars() {
      this.$Axios
        .get(this.$url + "/human/getAllVehicleDeviceInfo.do",{params:{
            t: Date.parse(new Date())
        }})
        .then(response => {
          // if(window.console){
          //     console.log(response);
          // }
          if (response.data.status) {
            this.cars = response.data.list;
            this.showCars = this.cars.filter((elem, index, arr) => {
              return index < 4;
            });
            this.carM = Math.ceil(this.cars.length / 4);
          }
        })
        .catch(error => {
          if (window.console) {
            console.log(error);
          }
        });
    },
    previousCar() {
      if (this.carN <= 1) {
        this.carN = 1;
      } else {
        --this.carN;
        this.showCars = this.cars.filter((elem, index, arr) => {
          return 4 * (this.carN - 1) <= index && index <= 4 * this.carN - 1;
        });
      }
    },
    nextCar() {
      if (this.carN >= Math.ceil(this.cars.length / 4)) {
        this.carN = Math.ceil(this.cars.length / 4);
      } else {
        ++this.carN;
        this.showCars = this.cars.filter((elem, index, arr) => {
          return 4 * (this.carN - 1) <= index && index <= 4 * this.carN - 1;
        });
      }
    },
    //环卫车轨迹回放
    showCarTrack(carImei) {
      this.$store.commit("trackShowImei", carImei);
      this.$store.commit("trackType", "car");
      this.carimeis = [];
    },
    //查询环卫车
    searchingCar() {
      this.searchCarResult = [];
      this.noSearchCar = false;
      if (this.searchCarText) {
        for (let i = 0; i < this.cars.length; i++) {
          if (this.cars[i].imei == this.searchCarText) {
            this.searchCarResult.push(this.cars[i]);
          }
        }
        if (this.searchCarResult.length) {
          this.searchCar = true;
          this.displayCar = false;
        } else {
          this.noSearchCar = true;
          this.displayCar = false;
        }
      }
    },
    //获取所有环卫机器人设备信
    getRobots() {
      this.$Axios
        .get(this.$url + "/human/getAllRobotDeviceInfo.do",{params:{
            t: Date.parse(new Date())
        }})
        .then(response => {
          // if(window.console){
          //     console.log(response);
          // }
          this.robots = response.data.list;
          this.showRobots = this.robots.filter((elem, index, arr) => {
            return index < 4;
          });
          this.robotM = Math.ceil(this.robots.length / 4);
        })
        .catch(error => {
          if (window.console) {
            console.log(error);
          }
        });
    },
    previousRobot() {
      if (this.robotN <= 1) {
        this.robotN = 1;
      } else {
        --this.robotN;
        this.showRobots = this.robots.filter((elem, index, arr) => {
          return 4 * (this.robotN - 1) <= index && index <= 4 * this.robotN - 1;
        });
      }
    },
    nextRobot() {
      if (this.robotN >= Math.ceil(this.robots.length / 4)) {
        this.robotN = Math.ceil(this.robots.length / 4);
      } else {
        ++this.robotN;
        this.showRobots = this.robots.filter((elem, index, arr) => {
          return 4 * (this.robotN - 1) <= index && index <= 4 * this.robotN - 1;
        });
      }
    },
    // 查询机器人
    searchingRobot() {
      this.searchRobotResult = [];
      this.noSearchRobot = false;
      if (this.searchRobotText) {
        for (let i = 0; i < this.robots.length; i++) {
          if (this.robots[i].imei == this.searchRobotText) {
            this.searchRobotResult.push(this.robots[i]);
          }
        }
        if (this.searchRobotResult.length) {
          this.searchRobot = true;
          this.displayRobot = false;
        } else {
          this.noSearchRobot = true;
          this.displayRobot = false;
        }
      }
    },
    showRobotTrack(robotImei) {
      this.$store.commit("trackShowImei", robotImei);
      this.$store.commit("trackType", "robot");
      this.robotimeis = [];
    },
    //获取所有智能垃圾桶
    getTrash() {
      this.$Axios
        .get(this.$url + "/human/getTrashInfo.do",{params:{
            t: Date.parse(new Date())
        }})
        .then(response => {
          // if(window.console){
          //     console.log(response);
          // }
          if (response.data.status) {
            this.trash = response.data.list;
            this.showTrash = this.trash.filter((elem, index, arr) => {
              return index < 4;
            });
            this.trashM = Math.ceil(this.trash.length / 4);
          }
        })
        .catch(error => {
          if (window.console) {
            console.log(error);
          }
        });
    },
    previousTrash() {
      if (this.trashN <= 1) {
        this.trashN = 1;
      } else {
        --this.trashN;
        this.showTrash = this.trash.filter((elem, index, arr) => {
          return 4 * (this.trashN - 1) <= index && index <= 4 * this.trashN - 1;
        });
      }
    },
    nextTrash() {
      if (this.trashN >= Math.ceil(this.trash.length / 4)) {
        this.trashN = Math.ceil(this.trash.length / 4);
      } else {
        ++this.trashN;
        this.showTrash = this.trash.filter((elem, index, arr) => {
          return 4 * (this.trashN - 1) <= index && index <= 4 * this.trashN - 1;
        });
      }
    },
    //查询垃圾桶
    searchingTrash() {
      this.searchTrashResult = [];
      this.noSearchTrash = false;
      if (this.searchTrashText) {
        for (let i = 0; i < this.trash.length; i++) {
          if (this.trash[i].imei == this.searchTrashText) {
            this.searchTrashResult.push(this.trash[i]);
          }
        }
        if (this.searchTrashResult.length) {
          this.searchTrash = true;
          this.displayTrash = false;
        } else {
          this.noSearchTrash = true;
          this.displayTrash = false;
        }
      }
    },
    //获取积水监测数据
    getWaterCamera() {
      this.$Axios
        .get(this.$url + "/human/getPondingInfo.do",{params:{
            t: Date.parse(new Date())
        }})
        .then(response => {
          // if(window.console){
          //     console.log(response);
          // }
          if (response.data.list) {
            this.waterCameras = response.data.list;
            this.showCamera = this.waterCameras.filter((elem, index, arr) => {
              return index < 4;
            });
            this.cameraM = Math.ceil(this.waterCameras.length / 4);
          }
        })
        .catch(error => {
          if (window.console) {
            console.log(error);
          }
        });
    },
    previousCamera() {
      if (this.cameraN <= 1) {
        this.cameraN = 1;
      } else {
        --this.cameraN;
        this.showCamera = this.waterCameras.filter((elem, index, arr) => {
          return (
            4 * (this.cameraN - 1) <= index && index <= 4 * this.cameraN - 1
          );
        });
      }
    },
    nextCamera() {
      if (this.cameraN >= Math.ceil(this.waterCameras.length / 4)) {
        this.cameraN = Math.ceil(this.waterCameras.length / 4);
      } else {
        ++this.cameraN;
        this.showCamera = this.waterCameras.filter((elem, index, arr) => {
          return (
            4 * (this.cameraN - 1) <= index && index <= 4 * this.cameraN - 1
          );
        });
      }
    },
    //查询某个积水监测设备
    searchingCamera() {
      this.searchCameraResult = [];
      this.noSearchCamera = false;
      if (this.searchCameraText) {
        for (let i = 0; i < this.waterCameras.length; i++) {
          if (this.waterCameras[i].imei == this.searchCameraText) {
            this.searchCameraResult.push(this.waterCameras[i]);
          }
        }
        if (this.searchCameraResult.length) {
          this.searchCamera = true;
          this.displayCamera = false;
        } else {
          this.noSearchCamera = true;
          this.displayCamera = false;
        }
      }
    }
  }
};
</script>
<style lang="less" scoped>
#control {
  width: 100%;
  height: 100%;
  .box {
    width: 100%;
    height: 50px;
    background-color: #1b1a1a;
    background-color: #031830;
    text-align: center;
    ul {
      height: 100%;
      width: 100%;
      margin: 0px;
      list-style-type: none;
      font-weight: bolder;
      li {
        width: 116px;
        height: 40px;
        margin-top: 10px;
        padding: 10px;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        float: left;
        cursor: pointer;
      }
    }
  }
  #controlNav {
    width: 350px;
    height: calc(100% - 110px);
    position: absolute;
    left: 0px;
    background-color: #1b1a1a;
    color: aliceblue;
    .displayList {
      width: 100%;
      height: 100%;
      background-color: #282729;
      background-color: #1a2647;
      padding: 5%;
      overflow: auto;
      ul {
        width: 100%;
        margin: 0px;
        list-style-type: none;
        border-radius: 5px;
        padding: 5%;
        li {
          height: 100px;
          width: 100%;
          border-radius: 5px;
          background-color: #283966;
          background-color: #283966;
          margin-bottom: 10px;
          .msg {
            height: 70%;
            width: 100%;
            border-bottom: 1px solid #dbdcdc;
            padding: 5%;
          }
          .operate {
            height: 30%;
            width: 100%;
            .cell {
              height: 100%;
              width: 50%;
              float: left;
              text-align: center;
              cursor: pointer;
              &:hover {
                color: #fd8403;
              }
              div {
                padding-top: 5px;
                height: 100%;
                width: 100%;
                input {
                  margin: 0px;
                }
              }
            }
          }
        }
        .trash {
          height: 70px;
          width: 100%;
          border-radius: 5px;
          background-color: #283966;
          margin-bottom: 10px;
          .msg {
            height: 100%;
            width: 100%;
            border-bottom: none;
            padding: 5%;
          }
        }
        .camera {
          height: 85px;
          width: 100%;
          border-radius: 5px;
          background-color: #283966;
          margin-bottom: 10px;
          .msg {
            height: 100%;
            width: 100%;
            border-bottom: none;
            padding: 5%;
          }
        }
      }
    }
  }
  #mapContainer {
    width: calc(100% - 350px);
    height: calc(100% - 50px);
    float: right;
    background-color: white;
    color: #666564;
    // overflow: hidden;
  }
}
</style>




