<template>
    <div id="trackView">
        <!-- 时间选择 -->
        <div id=trackNav>
            <span class="demonstration" style="font-weight:bold;margin-right:20px;">IMEI:{{trackShowImei}} </span>
            <span class="demonstration" style="font-weight:bold;margin-right:5px;">请选择回放的时间段：</span>
            <el-date-picker
            v-model="selectTime"
            unlink-panels
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">      
            </el-date-picker>
            <button type="button" class="btn btn-primary" @click="getTrackCoords">播放</button>
        </div> 
        <div id="speed">
            <div style="float:left;width:33.3%;height:100%;padding-top:10px;">
                <div style="float:left;">回放进度：</div>
                <div style="width:60%;float:left;">
                    <el-progress :text-inside="true" :stroke-width="13" :percentage="percentage"></el-progress>
                </div> 
                   
            </div>   
            <div style="float:left;width:33.3%;height:100%;">
                <div style="float:left;margin-left:0px;padding-top:8px;margin-right:10px;">回放速度：</div>
                 <div style="width:50%;float:left;">                  
                    <el-slider v-model="speed" input-size="mini" :show-tooltip="false" :max="490"></el-slider>
                </div> 
            </div>  
             <div style="float:left;width:33.3%;height:100%;padding:10px;">
                 <div style="float:left;width:33.3%;height:100%;">
                    <img src="../../assets/planet.png" alt="planet" height="100%"><span>卫星</span>
                 </div>
                 <div style="float:left;width:33.3%;height:100%;">
                    <img src="../../assets/base.png" alt="base" height="100%"><span>基站</span>
                 </div>
                 <div style="float:left;width:33.3%;height:100%;">
                    <img src="../../assets/wifi.png" alt="wifi" height="100%"><span>WIFI</span>
                 </div>
                
                
                
             </div>       
        </div>
        <!-- 地图 -->
        <div id="trackMap"></div>
        <!-- 信息提示模态框 -->
        <div class="modal fade bs-example-modal-sm" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog modal-sm" role="document" id="modal">
                <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <div class="modal-title" id="myModalLabel"><strong>操作提示</strong></div>
                </div>
                <div class="modal-body">
                    {{msg}}
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-primary" data-dismiss="modal">关闭</button>
                </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {styleJson} from '../../js/mapstyle.js'
import Qs from 'qs'
export default {
    name:'trackView',
    data(){
        return{
            map:null,
            selectTime: '',
            trackCoords:[],//选定时间段内的所有定位坐标
            markers:[],//在地图上的点标注
            points:[], //折线标注节点
            setTime:null,
            msg:'',//操作提示信息
            site:'',//定位的具体街道信息
            percentage:0,
            speed:250
        }
    },
    mounted(){
        this.showMap();
    },
    computed:{
        trackShowImei(){
            return this.$store.state.trackShowImei;
        },
        trackType(){
            return this.$store.state.trackType;
        }
    },
    methods:{
        showMap(){
            let map = new BMap.Map("trackMap");          // 创建地图实例  
            // let point = new BMap.Point(113.405167,22.81125);  // 大岗镇政府
            // let point = new BMap.Point(113.561854,23.131901); //黑牛
            let point = new BMap.Point(113.270758,23.135589);//广州市
            map.centerAndZoom(point, 11);  
            map.enableScrollWheelZoom(true);  
            map.setMapStyleV2({styleJson:styleJson});
            map.addControl(new BMap.NavigationControl({offset: new BMap.Size(20, 70)}));  
            map.addControl(new BMap.MapTypeControl({mapTypes: [BMAP_NORMAL_MAP,BMAP_HYBRID_MAP],offset: new BMap.Size(20,70),anchor:BMAP_ANCHOR_TOP_RIGHT}));   
            this.map=map;
        },
        //获取轨迹坐标
        getTrackCoords(){
            if(this.selectTime){
                //时间格式处理
                let begintime=`${this.selectTime[0].getFullYear()}-${this.selectTime[0].getMonth()+1}-${this.selectTime[0].getDate()} ${this.selectTime[0].getHours().toString().padStart(2, '0')}:${this.selectTime[0].getMinutes().toString().padStart(2, '0')}:${this.selectTime[0].getSeconds().toString().padStart(2, '0')}`;
                let endtime=`${this.selectTime[1].getFullYear()}-${this.selectTime[1].getMonth()+1}-${this.selectTime[1].getDate()} ${this.selectTime[1].getHours().toString().padStart(2, '0')}:${this.selectTime[1].getMinutes().toString().padStart(2, '0')}:${this.selectTime[1].getSeconds().toString().padStart(2, '0')}`;               
                //获取时间段内的所有经纬度
                let url;
                if(this.trackType=="worker"){
                    url=this.$url+"/human/getDeviceTrackInfo.do";
                }
                if(this.trackType=="car"){
                    url=this.$url+"/human/getDeviceVehicleTrackInfo.do";
                }
                if(this.trackType=="robot"){
                    url=this.$url+"/human/getDeviceRobotTrackInfo.do";
                }
                this.$Axios.post(url,Qs.stringify({
                            imei:this.trackShowImei,
                            begintime:begintime,
                            endtime:endtime
                        })).then((response)=>{
                            // if(window.console){
                            //     console.log(response);
                            // }
                            if(response.data.track){
                                this.trackCoords=response.data.track;
                                if(this.trackCoords.length==0){
                                    this.msg="该时间段内没有记录轨迹";
                                    $('#myModal').modal('show');
                                }else{
                                    this.drawTrack();
                                }                          
                            }else{
                                this.msg="轨迹数据为空";
                                $('#myModal').modal('show');
                            }                     
                        }).catch((error)=>{
                           if(window.console){
                                console.log(error);
                            }
                        })
            }else{
                this.msg="请选择回放的时间段";
                $('#myModal').modal('show');
                // alert("请选择要回放的时间段");
            }      
        },
        //画轨迹
        drawTrack(){
            clearTimeout(this.setTime);
            this.markers=[];
            this.points=[];
            this.map.clearOverlays();
            this.percentage=0;
            //添加开始标注
            let start= new BMap.Icon(require("../../assets/start1.png"), new BMap.Size(25, 39),{ anchor: new BMap.Size(12, 39)});
            this.map.addOverlay(new BMap.Marker(new BMap.Point(this.trackCoords[0].lng,this.trackCoords[0].lat),{icon: start}));
            //添加环卫工标注
            let imgUrl;
            let size;
            if(this.trackType=="worker"){
                imgUrl=require("../../assets/worker3.png");
                size=new BMap.Size(28, 28);
            }
            if(this.trackType=="car"){
                imgUrl=require("../../assets/car2.png");
                size=new BMap.Size(50,21);
            }
            if(this.trackType=="robot"){
                imgUrl=require("../../assets/robot4.png");
                size=new BMap.Size(39,28);
            }
            let myIcon = new BMap.Icon(imgUrl,size);
            let i=0;
            let imeiShow=this.trackImei;
            let _this=this;   
            function draw(){
                //添加停止标注
                if(i ==( _this.trackCoords.length-1)){
                    let stop= new BMap.Icon(require("../../assets/stop1.png"), new BMap.Size(25, 39),{ anchor: new BMap.Size(12, 39)});
                    _this.map.addOverlay(new BMap.Marker(new BMap.Point(_this.trackCoords[_this.trackCoords.length-1].lng,_this.trackCoords[_this.trackCoords.length-1].lat),{icon: stop}));        
                }
                if (i >= _this.trackCoords.length) {
                    _this.msg="回放完毕";
                    $('#myModal').modal('show');
                    return;
                }
                let point=new BMap.Point(_this.trackCoords[i].lng,_this.trackCoords[i].lat);
                //画标注
                let marker=new BMap.Marker(point,{icon: myIcon});
                _this.map.addOverlay(marker);
                //信息窗口
                let geoc = new BMap.Geocoder();  
                geoc.getLocation(point,(rs)=>{
                    let addComp = rs.addressComponents;
                    _this.site = addComp.province + addComp.city + addComp.district + addComp.street + addComp.streetNumber;
                })
                if(_this.trackCoords[i].gpsSpeed<0){_this.trackCoords[i].gpsSpeed="暂无速度"}
                let sContent=`
                        <div style="line-height:200%;">                      
                            <div>定位时间：${_this.trackCoords[i].gpsTime}</div>  
                            <div>速度：${_this.trackCoords[i].gpsSpeed}</div>  
                            <div>定位地点：${_this.site}</div>                     
                        </div>`          
                let InfoWindow = new BMapLib.SearchInfoWindow(_this.map, sContent, {
                    title:"IMEI:"+ imeiShow, //标题
                    width: 250, //宽度
                    height: 100, //高度
                    panel : "panel", //检索结果面板
                    enableAutoPan : true, //自动平移
                    enableSendToPhone: false,
                    searchTypes :[
                    ]
                });
                InfoWindow.open(point); 
                marker.addEventListener("click", function(){    
                    InfoWindow.open(point); 
                }); 
                //清楚上一个标注
                _this.markers.push(marker);            
                if(_this.markers.length>1){
                    _this.map.removeOverlay(_this.markers[i-1]);
                }
                //画线
                _this.points.push(point);
                _this.map.addOverlay(new BMap.Polyline( _this.points, {
								strokeColor: "#fafbfd",
								strokeWeight: 3,
								strokeOpacity: 1
                }));             
                i++;
                _this.percentage=Math.round((i/ _this.trackCoords.length)*100);
                _this.setTime=setTimeout(draw,500 - _this.speed);
            }
            draw();
        }
    }
   
}
</script>
<style lang="less" scoped>
#trackView{
    width: 100%;
    height: 100%;
    #trackNav{
        height:50px;
        width: calc(100% - 350px);
        position: absolute;
        z-index: 2;
        // background-color: rgb(17, 1, 105);
        background-color: white;
        // color: aliceblue;
        color: #666564;
        padding:5px;
    }
    #speed{
        height:50px;
        width:50%;
        position: absolute;
        z-index:1;
        top: 160px;
        left: calc(350px + 15%);
        background-color: white;
        border:1px solid #bbbaba;
        border-bottom-left-radius:50px;
        border-bottom-right-radius:50px;
        padding:5px 30px;
    }
    #trackMap{
        width: 100%;
        height: 100%;
    }
    #modal{
        margin-top: 210px;
    }
}
</style>




