<template>
    <div id="kaoqin">
        <div id="kqTitle" class="box">环卫人员考勤记录</div>
        <div class="box"><span>在岗人数{{onLine}}人</span><span style="margin-left:7%;">休班人数：{{onRest}}人</span></div>
        <div class="box">迟到：{{late}}人</div>
        <div class="box">早退：{{leaveEarly}}人</div>
        <div class="box">脱岗：{{outLine}}人</div>  
        <table>
            <caption>打卡记录</caption>
            <thead>           
                <tr>
                    <td>姓名</td><td>签到时间</td><td>签退时间</td><td>状态</td>
                </tr>
            </thead>
            <tbody>
                <tr v-for="attend in attendance" v-bind:key="attend.id">
                    <td>{{attend.name}}</td><td>{{attend.start}}</td><td>{{attend.end}}</td><td>{{attend.status}}</td>
                </tr>
            </tbody>
        </table>
    </div>
</template>
<script>
export default {
    name:'kaoqin',
    data(){
        return{
            // attendance:[
            //     {name:'田心建',start:'8:58',end:'18:03',status:'正常'},
            //     {name:'陈文芳',start:'9:00',end:'18:05',status:'正常'},
            //     {name:'王建国',start:'9:12',end:'18:01',status:'迟到'}
            // ]
        }
    },
    computed:{
        onLine(){
            return this.$store.state.workers.onLine;
        },
        onRest(){
            return this.$store.state.workers.onRest;
        },
        late(){
            return this.$store.state.workers.late;
        },
        leaveEarly(){
            return this.$store.state.workers.leaveEarly;
        },
        outLine(){
            return this.$store.state.workers.outLine;
        },
        attendance(){
            return this.$store.state.workers.attendance;

        }
    }

}
</script>
<style lang="less" scoped>
#kaoqin{
    height: 100%;
    width: 100%;
    color: #666564;
    .box{
        width: 100%;
        height: 50px;
        padding-top: 15px;
        padding-left: 20px;
        font-weight: bold;
        border-bottom:1px solid #b1b0b0;;
    }
    table{
        width: 100%;
        text-align: center;
        caption{
            width: 100%;
            height: 50px;
            text-align: center;
            color:#666564;
            font-weight: bold;
            padding-top: 20px;
        }
        
        thead{
            background-color: #dadae2; 
            font-weight: bold;        
        }
        td{
            height: 50px;
            border:1px solid #b1b0b0;
        }
    }
}

</style>


