<template>
    <div id="manage">
        <div id="manageTitle">
            <ul>
                <li :style="{backgroundColor:bgColor[0],color:color[0]}" @click="changeBgColor(0)">环卫人员</li>
                <li :style="{backgroundColor:bgColor[1],color:color[1]}" @click="changeBgColor(1)">环卫车</li>
                <li :style="{backgroundColor:bgColor[2],color:color[2]}" @click="changeBgColor(2)">环卫机器人</li>
            </ul>
            
        </div>
        <!-- 环卫人员设备信息 -->
        <div class="tableContainer" v-if="showList[0]">
           <table>
                <caption>环卫人员设备信息列表</caption>
                <thead>
                    <tr>
                        <td>设备ID</td><td>设备名称</td><td>IMEI</td><td>设备类型</td><td>激活时间</td><td>到期时间</td><td>设备SIM卡号</td><td>开机状态</td><td>是否已绑定用户</td><td>操作</td>
                    </tr>
                </thead>
                <tbody>               
                    <tr v-for="device in devices" v-bind:key="device.id">
                        <td>{{device.id}}</td><td>{{device.deviceName}}</td><td>{{device.imei}}</td><td>{{device.mcType}}</td><td>{{device.activationTime}}</td><td>{{device.expiration}}</td><td>{{device.sim}}</td><td>{{device.status}}</td><td>{{device.username}}</td><td><button type="button"  class="btn btn-sm btn-primary" data-toggle="modal" data-target="#deviceModal" @click="sendDeviceId(device.id)" v-if="device.username=='未绑定'">绑定用户</button><button type="button"  class="btn btn-sm btn-danger" v-if="device.username!='未绑定'" @click="unBingDing(device.id)">解除绑定</button></td>
                    </tr>
                </tbody>
            </table>
        </div>
        <!-- 未绑定环卫人员模态框 -->
        <div class="modal fade" id="deviceModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <h4 class="modal-title" id="myModalLabel" style="font-weight:bold;">绑定用户</h4>
                </div>
                <div class="modal-body" id="modalBody">
                  <table>
                      <thead>
                          <tr>
                              <td>姓名</td><td>性别</td><td>年龄</td><td>社区</td><td>操作</td>
                          </tr>
                      </thead>
                      <tbody>
                          <tr v-for="worker in workers" :key="worker.id">
                              <td>{{worker.username}}</td><td>{{worker.sex}}</td><td>{{worker.age}}</td><td>{{worker.community}}</td><td><button @click="sendWorkerId(worker.id)" :style="{backgroundColor:workerBgColor[worker.id]}">绑定</button></td>
                          </tr>
                      </tbody>
                  </table>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-primary" @click="bangDing">保存</button>
                </div>
                </div>
            </div>
        </div>
        <!-- 环卫车设备信息 -->
        <div class="tableContainer" v-if="showList[1]">
            <table>
                <caption>环卫车设备信息列表</caption>
                <thead>
                    <tr>
                        <td>环卫车号牌</td><td>设备ID</td><td>设备名称</td><td>IMEI</td><td>设备类型</td><td>激活时间</td><td>到期时间</td><td>开机状态</td><td>是否已绑定用户</td><td>操作</td>
                    </tr>
                </thead>
                <tbody>               
                    <tr v-for="car in carDevices" v-bind:key="car.id">
                        <td>{{car.sim}}</td><td>{{car.id}}</td><td>{{car.deviceName}}</td><td>{{car.imei}}</td><td>{{car.mcType}}</td><td>{{car.activationTime}}</td><td>{{car.expiration}}</td><td>{{car.status}}</td><td>{{car.username}}</td><td><button type="button"  class="btn btn-sm btn-primary" data-toggle="modal" data-target="#driverModal" @click="sendCarId(car.id)" v-if="car.username=='未绑定'">绑定驾驶员</button><button type="button"  class="btn btn-sm btn-danger" v-if="car.username!='未绑定'" @click="unBingDriver(car.id)">解除绑定</button></td>
                    </tr>
                </tbody>
            </table>
       </div>
       <!-- 未绑定驾驶员模态框 -->
        <div class="modal fade" id="driverModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <h4 class="modal-title" id="myModalLabel" style="font-weight:bold;">绑定驾驶员</h4>
                </div>
                <div class="modal-body" id="modalBody">
                  <table>
                      <thead>
                          <tr>
                              <td>姓名</td><td>性别</td><td>年龄</td><td>社区</td><td>操作</td>
                          </tr>
                      </thead>
                      <tbody>
                          <tr v-for="driver in drivers" :key="driver.id">
                              <td>{{driver.username}}</td><td>{{driver.sex}}</td><td>{{driver.age}}</td><td>{{driver.community}}</td><td><button @click="sendDriverId(driver.id)" :style="{backgroundColor:driverBgcolor[driver.id]}">绑定</button></td>
                          </tr>
                      </tbody>
                  </table>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-primary" @click="bingDriver">保存</button>
                </div>
                </div>
            </div>
        </div>
       <div class="tableContainer" v-if="showList[2]">
            <table>
                <caption>环卫机器人设备信息列表</caption>
                <thead>
                    <tr>
                        <td>设备ID</td><td>设备名称</td><td>IMEI</td><td>设备类型</td><td>激活时间</td><td>到期时间</td><td>开机状态</td>
                    </tr>
                </thead>
                <tbody>               
                    <tr v-for="robot in robotDevices" v-bind:key="robot.id">
                        <td>{{robot.id}}</td><td>{{robot.deviceName}}</td><td>{{robot.imei}}</td><td>{{robot.mcType}}</td><td>{{robot.activationTime}}</td><td>{{robot.expiration}}</td><td>{{robot.status}}</td>
                    </tr>
                </tbody>
            </table>
       </div>
       <!-- 信息提示模态框 -->
        <div class="modal fade bs-example-modal-sm" id="tipsModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog modal-sm" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <div class="modal-title" id="myModalLabel"><strong>操作提示</strong></div>
                </div>
                <div class="modal-body">
                    {{msg}}
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-primary" data-dismiss="modal">关闭</button>
                </div>
                </div>
            </div>
        </div>   
    </div>
</template>
<script>
import Qs from 'qs'
export default {
    name:"manage",
    data(){
        return{
            bgColor:['#1c2a4f','','','',''],
            color:['#fd8403','white','white','white','white'],
            showList:[true,false,false],
            msg:'',
            //环卫工设备
            devices:[],
            deviceId:'',//需要绑定的设备ID
            workers:[], //未绑定人员信息
            workerId:'',//需要绑定的人员ID
            workerBgColor:[],
            type:'card',
            //环卫车
            carDevices:[],//所有环卫车设备信息
            carDeviceId:'',//需要绑定的环卫车设备ID
            drivers:[],//未绑定驾驶人员信息
            driverId:'',//需要绑定的驾驶员ID
            driverBgcolor:[],
            //环卫机器人
            robotDevices:[],
            //智能手环
            bracelet:[]
        }
    },
    created(){
        this.getDevice();
        // this.getBracelet();
        this.getCarDevice();
        this.getRobotDevice();
    },
    methods:{
        changeBgColor(n){
            this.showList=[false,false,false];
            this.showList[n]=true;
            this.bgColor=[],
            this.bgColor[n]="#1c2a4f";
            this.color=['white','white','white','white','white'];
            this.color[n]='#fd8403';           
        },
        //获取所有环卫工设备信息
        getDevice(){
            this.$Axios.get(this.$url+'/human/getAllDeviceInfo.do',{params:{
                            t: Date.parse(new Date())
                        }})
                        .then((response)=>{
                            if(response.data.status){
                                this.devices=response.data.list;
                            }                      
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
        },
        //获取手环设备信息
        // getBracelet(){
        //     this.$Axios.get(this.$url+"/bracelet/getBraceletPeopleData.do")
        //                 .then((response)=>{
        //                     if(window.console){
        //                         console.log(response);                           
        //                     }
        //                     if(response.data.status){
        //                         this.bracelet=response.data.list;
        //                         this.devices=this.devices.concat(this.bracelet);
        //                     }
        //                 }).catch((error)=>{
        //                     if(window.console){
        //                         console.log(error);
        //                     }
        //                 })
        // },
        //绑定某设备ID并获取未绑定人员信息
        sendDeviceId(deviceId){
            this.deviceId=deviceId;
            // if(heartrate){
            //     this.type="bracelet";
            // }else{
            //     this.type="card";
            // }
            this.$Axios.get(this.$url+'/human/getAllUnboundDevicePeople.do',{params:{
                            t: Date.parse(new Date())
                        }})  
                        .then((response)=>{
                            this.workers=response.data.list;
                            //解析性别
                            for(let i=0;i<this.workers.length;i++){
                                if(this.workers[i].sex){
                                    this.workers[i].sex="男";
                                }else{
                                    this.workers[i].sex="女";
                                }
                            }
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
        },
        sendWorkerId(workerId){
            this.workerId=workerId;
            this.workerBgColor=[];
            this.workerBgColor[workerId]="#337ab7";
        },
        bangDing(){  
            // if(this.type=="card"){ 
                if(this.workerId){
                    $('#deviceModal').modal('hide');
                    this.$Axios.post(this.$url+'/human/bindingDeviceByPeople.do',Qs.stringify({
                                    deviceId:this.deviceId,
                                    humanId:this.workerId   
                                }))
                                .then((response)=>{                                   
                                    if(response.data.status){
                                        // alert(response.data.msg);
                                        this.getDevice();
                                        // this.getBracelet();
                                    }
                                    
                                })
                                .catch((error)=>{
                                    if(window.console){
                                        console.log(error);
                                    }
                                })
                    //清空
                    this.workerBgColor=[]; 
                    this.deviceId="";
                    this.workerId="";       
                }else{
                    this.msg="请选择需要绑定的用户";
                    $("#tipsModal").modal("show");
                }    
            // }
            // if(this.type=="bracelet"){
            //     if(this.workerId){
            //         $('#deviceModal').modal('hide');
            //         this.$Axios.post(this.$url+"/bracelet/bindingDeviceByBracelet.do",Qs.stringify({
            //             deviceId:this.deviceId,
            //             humanId:this.workerId
            //         })).then((response)=>{
            //             if(response.data.status){
            //                  this.getDevice();
            //                  this.getBracelet();
            //             }
            //         }).catch((error)=>{
            //             if(window.console){
            //                 console.log(error);
            //             }
            //         })
            //         //清空
            //         this.workerBgColor=[]; 
            //         this.deviceId="";
            //         this.workerId="";  
            //     }else{
            //         this.msg="请选择需要绑定的用户";
            //         $("#tipsModal").modal("show");
            //     } 
            // }
        },
        //解除绑定
        unBingDing(deviceId){
            let okey= window.confirm("是否确定要解除绑定？");
            if(okey){
                // if(heartrate){
                //     this.$Axios.post(this.$url+"/bracelet/relieveBindingDevice.do",Qs.stringify({
                //         id:deviceId
                //     })).then((response)=>{
                //         this.getDevice();
                //         // this.getBracelet();
                //     }).catch((error)=>{
                //         if(window.console){
                //             console.log(error);
                //         }
                //     })
                // }else{
                    this.$Axios.post(this.$url+'/human/relieveBindingDevice.do',Qs.stringify({
                            deviceId:deviceId,                        
                        })).then((response)=>{
                            this.getDevice();
                            // this.getBracelet();
                        }).catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
                // }
                
            }   
        },
        //获取环卫车设备信息
        getCarDevice(){
            this.$Axios.get(this.$url+'/human/getAllVehicleDeviceInfo.do',{params:{
                            t: Date.parse(new Date())
                        }})
                        .then((response)=>{
                            // if(window.console){
                            //     console.log(response);
                            // }
                            if(response.data.status){
                                this.carDevices=response.data.list;
                            }                      
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
        },
        sendCarId(carId){
            this.carDeviceId=carId;
            this.$Axios.get(this.$url+'/human/getAllUnboundDeviceVehicle.do',{params:{
                            t: Date.parse(new Date())
                        }})  
                        .then((response)=>{
                            // if(window.console){
                            //     console.log(response);
                            // }
                            if(response.data.list){
                                this.drivers=response.data.list;
                                //解析性别
                                for(let i=0;i<this.drivers.length;i++){
                                    if(this.drivers[i].sex){
                                        this.drivers[i].sex="男";
                                    }else{
                                        this.drivers[i].sex="女";
                                    }
                                }
                            }
                            
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })

        },
        sendDriverId(driverId){
            this.driverId=driverId;
            this.driverBgcolor=[];
            this.driverBgcolor[driverId]="#337ab7";
        },
        bingDriver(){
            if(this.driverId){
            $('#driverModal').modal('hide');
            this.$Axios.post(this.$url+'/human/bindingDeviceByVehicle.do',Qs.stringify({
                            deviceId:this.carDeviceId,
                            humanId:this.driverId   
                        }))
                        .then((response)=>{                          
                            if(response.data.status){
                                this.getCarDevice();
                            }                           
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
            //清空
            this.driverBgcolor=[]; 
            this.carDeviceId="";
            this.driverId="";       
            }else{
                this.msg="请选择需要绑定的用户";
                $("#tipsModal").modal("show");
            }    
        },
        unBingDriver(carId){
            let okey= window.confirm("是否确定要解除绑定？");
            if(okey){
                this.$Axios.post(this.$url+'/human/relieveBindingDeviceVehicle.do',Qs.stringify({
                            deviceId:carId,                        
                        }))
                        .then((response)=>{
                            this.getCarDevice();
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
            }   
        },
        //获取所有机器人设备信息
        getRobotDevice(){
            this.$Axios.get(this.$url+"/human/getAllRobotDeviceInfo.do")
                        .then((response)=>{
                            // if(window.console){
                            //     console.log(response);                           
                            // }
                            this.robotDevices=response.data.list;
                        }).catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
        }
        
    }
}
</script>
<style lang="less" scoped>
#manage{
    width: 100%;
    height: 100%;
    background-color: white;
    #manageTitle{
        width: 100%;
        height: 50px;
        background-color: #1b1a1a;
        background-color: #031830;
        ul{
            height: 100%;
            width: 100%;
            margin:0px;
            list-style-type: none;
            font-weight: bolder;
            li{
                width:116px;
                height: 40px;
                text-align: center;
                margin-top: 10px;
                padding:10px;
                border-top-left-radius:5px;
                border-top-right-radius: 5px;
                float:left;
                cursor: pointer;                    
            }
        }
    }
    .tableContainer{
        width: 100%;
        height:calc(100% - 50px);
        overflow:auto;        
    }
    
    table{
        width: 100%;   
        text-align: center;
        caption{
            width: 100%;
            height: 50px;
            text-align: center;
            color:#666564;
            font-weight: bold;
            padding-top: 20px;
        }
        
        thead{
            background-color: #dadae2; 
            font-weight: bold;        
        }
        td{
            height: 50px;
            border:1px solid #b1b0b0;
        }
    }
    .modal-dialog{
        margin-top: 7%;
        #modalBody{
            height:400px;
            overflow: auto;
        }
    }
}
</style>





