import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
export default new Vuex.Store({
    state:{
        workers:{
            //环卫人员信息
            locationImeis:[], //控制台定位的设备imei
            //考勤情况
            onLine:3,
            onRest:5,
            late:1,
            leaveEarly:0,
            outLine:0,
            attendance:[
                {id:0,name:'田心建',start:'8:58',end:'18:03',status:'正常'},
                {id:1,name:'陈文芳',start:'9:00',end:'18:05',status:'正常'},
                {id:2,name:'王建国',start:'9:12',end:'18:02',status:'迟到'}
            ]
        },
        cars:{
            locationImeis:[],//控制台定位的设备imei
            //出勤情况
            onLine:4,
            onRest:4,
            attendance:[
                {id:0,licenseNum:'粤A1548',start:'8:54',end:'18:03',status:'正常'},
                {id:1,licenseNum:'粤A6584',start:'8:57',end:'17:05',status:'早退'},
                {id:2,licenseNum:'粤A8452',start:'9:00',end:'18:08',status:'正常'},
                {id:3,licenseNum:'粤A4266',start:'8:50',end:'17:16',status:'正常'}
            ]
        },
        trackShowImei:null,//要显示轨迹的设备imei
        trackType:null,//显示的轨迹的类型
        robots:{
            locationImeis:[],
            //环卫机器人信息
            num:8,
            ownNum:8,
            lackNum:0,

        },        
        devices:[],
        //智能垃圾桶
        sendtrash:[],
        //积水监测设备
        sendCamera:[]
    },
    mutations:{
        saveWorkers(state,workers){
            state.workers=workers;
        },
        saveCars(state,cars){
            state.cars=cars;
        },
        //所有的设备信息
        saveDevices(state,devices){
            state.devices=devices;
        },
        //控制台环卫工定位的设备ID
        LocationImei(state,imeis){
            state.workers.locationImeis=imeis;
        },
        //环卫车
        locationCar(state,imeis){
            state.cars.locationImeis=imeis;
        },
        // 环卫机器人
        locationRobot(state,imeis){
            state.robots.locationImeis=imeis;
        },
        //要显示轨迹的设备imei
        trackShowImei(state,imei){
            state.trackShowImei=imei;
        },
        trackType(state,type){
            state.trackType=type;
        },
        //智能垃圾桶
        locationTrash(state,sendtrash){
            state.sendtrash=sendtrash;
        },
        //积水监测设备
        locationCamera(state,sendCamera){
            state.sendCamera=sendCamera;
        }
    }
})


