<template>
    <div id="users">
        <div id="usersNavs">
            <!-- <ul class="nav nav-tabs">
                <li role="presentation" @click="changBgColor(0)"  class="active"><router-link tag="a" :to="{name:'Worker'}">环卫人员</router-link></li>
                <li role="presentation" @click="changBgColor(1)"><router-link tag="a" :to="{name:'Car'}">环卫车</router-link></li>
                <li role="presentation" @click="changBgColor(2)"><router-link tag="a" :to="{name:'Robot'}">环卫机器人</router-link></li>
            </ul> -->

            <ul>
                <li @click="changColor(0)"><router-link tag="div" :to="{name:'Worker'}" v-bind:style="{backgroundColor:bgColor[0],color:color[0]}">环卫人员</router-link></li>
                <li @click="changColor(1)"><router-link tag="div" :to="{name:'Car'}" v-bind:style="{backgroundColor:bgColor[1],color:color[1]}">环卫车</router-link></li>
                <li @click="changColor(2)"><router-link tag="div" :to="{name:'Robot'}" v-bind:style="{backgroundColor:bgColor[2],color:color[2]}">环卫机器人</router-link></li>
            </ul>
            
        </div>
        <div id="main">
            <router-view></router-view> 
        </div>       
    </div>
</template>
<script>
export default {
    name:"users",
    data(){
        return{
            bgColor:['#1c2a4f','',''],
            color:['#fd8403','white','white'],         
        }
    },
    created(){

    },
    mounted(){

    },
    methods:{

        changBgColor(n){
            $("#usersNavs").find("li").removeClass("active");
            $("#usersNavs").find("li")[n].setAttribute("class","active");
        },
        changColor(n){
            this.bgColor=[],
            this.bgColor[n]="#1c2a4f";
            this.color=['white','white','white'];
            this.color[n]='#fd8403';
        }
    }

}
</script>
<style lang="less" scoped>
#users{
    height: 100%;
    width: 100%;
    #usersNavs{
        height: 50px;
        width: 100%;
        // background-color:#070247;
        background-color: #1b1a1a;
        background-color: #031830;
        // ul{
        //     padding-top:10px;
        //     border:0px;
        // }
        ul{
            height: 100%;
            width: 350px;
            list-style-type: none;
            padding-top: 10px;
            li{
                display: block;
                float: left;
                width: 33.3%;
                height: 100%;
                text-align: center;               
                div{
                    cursor: pointer;
                    height: 100%;
                    width: 100%;
                    padding-top: 10px;
                    color: white;
                    font-weight: bolder;
                    // border-radius: 5px;
                    border-top-left-radius:5px;
                    border-top-right-radius: 5px;
                }
            }
        }
    }
    #main{
        width: 100%;
        height:calc(100% - 50px);
    }
    
}
</style>





