<template>
    <div id="huizong">
        <div id="HZtitle" class="box">环卫人员管理</div>
        <!-- <div class="box">核定人员编制数：{{num}}人</div>
        <div class="box"><span>现有：{{ownNum}}人 </span><span style="margin-left:7%;">暂缺：{{lackNum}}人</span></div> -->
        <div class="box">   
            <button type="button" class="btn btn-primary btn-sm" data-toggle="modal" data-target="#myModal">新增成员</button>  
            <span id="refresh" class="glyphicon glyphicon-repeat" aria-hidden="true" @click="getWorkers"></span>
        </div>
        <!-- 新增成员信息填写模态框 -->
        <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <h4 class="modal-title" id="myModalLabel" style="font-weight:bold;">新增成员</h4>
                </div>
                <div class="modal-body">
                    <form id="workerForm">
                        <div class="form-group">
                            <label for="name">姓名</label>
                            <input type="text" name="username" class="form-control" id="name" v-model="username">
                        </div>
                        <div class="form-group">
                            <label>性别</label><br>
                            <label class="radio-inline">
                                <input type="radio" name="inlineRadioOptions" id="1" value="1" v-model="sex">男
                                </label>
                                <label class="radio-inline">
                                <input type="radio" name="inlineRadioOptions" id="0" value="0" v-model="sex">女
                            </label>
                        </div>                      
                        <div class="form-group">
                            <label for="age">年龄</label>
                            <input type="text" name="age" class="form-control" id="age" v-model="age">
                        </div>
                        <div class="form-group">
                            <label for="phone">联系方式</label>
                            <input type="text" name="phone" class="form-control" id="phone" v-model="phone">
                        </div>
                        <div class="form-group">
                            <label>作业社区</label><br>
                            <select class="form-control" v-model="community">
                                <option v-for=" street in streetNames" :key="street.id" :value="street.streetName">{{street.streetName}}</option>                                
                            </select>
                        </div>
                        <div class="form-group">
                            <label for="rtsp">流地址</label>
                            <input type="text" name="rtsp" class="form-control" id="rtsp" v-model="rtspUrl">
                        </div>
                    </form>

                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-primary" @click="save">保存</button>
                </div>
                </div>
            </div>
        </div>
        <!-- <div class="box">在岗人员信息：</div> -->
        <div id="tableBox">
            <table>
                <caption>环卫人员信息</caption>
                <thead>           
                    <tr>
                        <td>姓名</td><td>性别</td><td>年龄</td><td>作业社区</td><td>联系方式</td><td>流地址</td><td>操作</td>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="worker in workers" v-bind:key="worker.id">
                        <td>{{worker.username}}</td><td>{{worker.sex}}</td><td>{{worker.age}}</td><td>{{worker.community}}</td><td>{{worker.phone}}</td><td style="max-width:120px;word-wrap:break-word;">{{worker.rtsp}}</td><td style="max-width:70px;"><button type="button"  class="btn btn-sm btn-default" @click="edit(worker)">编辑</button><button type="button"  class="btn btn-sm btn-primary" @click="deleteWorker(worker.id)">删除</button></td>
                    </tr>
                </tbody>
            </table>
        </div>
        <!-- 编辑模态框 -->
        <div class="modal fade" id="editModal" tabindex="-1" role="dialog" aria-labelledby="editModalLabel">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <h4 class="modal-title" id="editModalLabel" style="font-weight:bold;">编辑信息</h4>
                </div>
                <div class="modal-body">
                    <form id="workerForm">
                        <div class="form-group">
                            <label for="name">姓名</label>
                            <input type="text" name="username" class="form-control" id="name" v-model="editName">
                        </div>
                        <div class="form-group">
                            <label>性别</label><br>
                            <label class="radio-inline">
                                <input type="radio" name="inlineRadioOptions" id="1" value="1" v-model="editSex">男
                                </label>
                                <label class="radio-inline">
                                <input type="radio" name="inlineRadioOptions" id="0" value="0" v-model="editSex">女
                            </label>
                        </div>                      
                        <div class="form-group">
                            <label for="age">年龄</label>
                            <input type="text" name="age" class="form-control" id="age" v-model="editAge">
                        </div>
                        <div class="form-group">
                            <label for="phone">联系方式</label>
                            <input type="text" name="phone" class="form-control" id="phone" v-model="editPhone">
                        </div>
                        <div class="form-group">
                            <label>作业社区</label><br>
                            <select class="form-control" v-model="editCommunity">
                                <option v-for=" street in streetNames" :key="street.id" :value="street.streetName">{{street.streetName}}</option>                                
                            </select>
                        </div>
                        <div class="form-group">
                            <label for="rtsp">流地址</label>
                            <input type="text" name="rtsp" class="form-control" id="rtsp" v-model="editRtsp">
                        </div>
                    </form>

                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-primary" @click="saveEdit">保存</button>
                </div>
                </div>
            </div>
        </div>
        <!-- 提示模态框 -->
        <div class="modal fade bs-example-modal-sm" id="tipsModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog modal-sm" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <div class="modal-title" id="myModalLabel"><strong>操作提示</strong></div>
                    </div>
                    <div class="modal-body">
                        {{msg}}
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" data-dismiss="modal">关闭</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import Qs from 'qs'
export default {
    name:'huizong',
    data(){
        return{
            streetNames:[],//社区街道
            username:'',
            sex:'',
            age:'',
            phone:'',
            community:'',
            rtspUrl:'',
            workers:[],
            editWorker:[],
            //编辑信息
            editID:'',
            editName:'',
            editSex:'',
            editAge:'',
            editPhone:'',
            editCommunity:'',
            editRtsp:'',
            msg:''
            
        }
    },
    created(){
        this.getWorkers();
        this.getStreetNames();
    },
    computed:{

    },
    methods:{
        //获取社区名字
        getStreetNames(){
            this.$Axios.get(this.$url+'/human/getDropList_Community.do')
                        .then((response)=>{
                            this.streetNames=response.data.droplist;
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
        },
        //新增成员
        save(){
            if(this.username && this.age && this.sex!="" && this.phone && this.community && this.rtspUrl){
                $('#myModal').modal('hide');   
                this.$Axios.get(this.$url+'/human/editHumanSSInfo.do',{
                    params:{
                        username:this.username,
                        age:this.age,
                        phone:this.phone,
                        community:this.community,
                        sex:this.sex,
                        rtsp:this.rtspUrl
                    }
                })
                .then((response)=>{
                    if(response.data.status){
                        this.msg=response.data.msg;
                        $("#tipsModal").modal("show");
                    }else{
                        this.msg=response.data.msg;
                        $("#tipsModal").modal("show");
                    }
                    this.getWorkers();
                    this.username="";this.age="";this.sex="";this.phone="";this.community="";this.rtspUrl="";
                })
                .catch((error)=>{
                    if(window.console){
                        console.log(error);
                    }
                })
            }else{
                // alert("请填写完整的信息");
                this.msg="请填写完整的信息";
                $("#tipsModal").modal("show");
            }
        },
        //获取所有环卫人员信息
        getWorkers(){ 
            this.$Axios.get(this.$url+'/human/getHumanSSInfo.do',{params:{
                            t: Date.parse(new Date())
                        }})
                        .then((response)=>{
                            this.workers=response.data.list;                          
                            //解析性别
                            // for(let i=0;i<this.workers.length;i++){
                            //     if(this.workers[i].sex){
                            //         this.workers[i].sex="男"
                            //     }else{
                            //         this.workers[i].sex="女"
                            //     }
                            // }
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        }) 
        },
        //删除某个环卫工信息
        deleteWorker(id){
            this.$Axios.get(this.$url+'/human/delHumanSSInfo.do',{
                params:{
                    id:id
                }
            })
            .then((response)=>{
                if(response.data.status){
                    this.getWorkers();
                    this.msg=response.data.msg;
                    $("#tipsModal").modal("show");
                    
                }else{
                    this.msg=response.data.msg;
                    $("#tipsModal").modal("show");
                    this.getWorkers();
                }
            })
            .catch((error)=>{
                if(window.console){
                    console.log(error);
                }
            })           
        },
        //编辑某个环卫工信息
        edit(worker){
            this.editID=worker.id;
            this.editName=worker.username;
            this.editSex=worker.sex;
            if(this.editSex=="女"){
                this.editSex=0;
            }
            if(this.editSex=="男"){
                this.editSex=1;
            }
            this.editAge=worker.age;
            this.editPhone=worker.phone;
            this.editCommunity=worker.community;
            this.editRtsp=worker.rtsp;
            $("#editModal").modal("show");
        },
        // 保存编辑后的信息
        saveEdit(){
            if(this.editName && this.editAge && this.editPhone && this.editCommunity && this.editRtsp){
                $("#editModal").modal("hide");  
                this.$Axios.get(this.$url+'/human/editHumanSSInfo.do',{
                    params:{
                        id:this.editID,
                        username:this.editName,
                        age:this.editAge,
                        phone:this.editPhone,
                        community:this.editCommunity,
                        sex:this.editSex,
                        rtsp:this.editRtsp
                    }
                })
                .then((response)=>{
                    if(response.data.status){
                        this.getWorkers();
                    }else{
                        this.msg="编辑失败";
                        $("#tipsModal").modal("show");
                    }
                    
                })
                .catch((error)=>{
                   if(window.console){
                        console.log(error);
                    }
                })
            }else{
                this.msg="请填写完整的信息";
                $("#tipsModal").modal("show");
            }
        }
    }
}
</script>
<style lang="less" scoped>
#huizong{
    height:100%;
    width: 100%;
    color: #666564;
    #HZtitle{
        background-color: #dadae2;
    }
    .box{
        width: 100%;
        height: 50px;
        padding-top: 15px;
        padding-left: 20px;
        font-weight: bold;
        border-bottom:1px solid #b1b0b0;
        padding-right: 20px;
        #refresh{
            float: right;
            &:hover{
                cursor: pointer;
            }
        }
    }
    //模态框位置
    .modal-dialog{
        margin-top:7%;
    }
    #tableBox{
        width: 100%;
        height: 80%;
        overflow:auto;
        table{
            width: 100%;   
            text-align: center;
            caption{
                width: 100%;
                height: 50px;
                text-align: center;
                color:#666564;
                font-weight: bold;
                padding-top: 20px;
            }
            
            thead{
                background-color: #dadae2; 
                font-weight: bold;        
            }
            td{
                height: 50px;
                border:1px solid #b1b0b0;
                button{
                    margin-right: 20px;
                }
            }
        }
    }
    
}

</style>




