<template>
    <div id="safe">
        <div id="safeNav">
            <div id="navList">
                <ul>
                    <li :style="{backgroundColor:bgColor[0],color:color[0]}" @click="changeBgColor(0)">区域预警</li>
                    <li :style="{backgroundColor:bgColor[1],color:color[1]}" @click="changeBgColor(1)">标注点</li>
                    <li :style="{backgroundColor:bgColor[2],color:color[2]}" @click="changeBgColor(2)">共享风险点</li>
                </ul>
            </div>
            <div id="dispalyList">
                <div class="disply" v-if="show[0]">
                    <div class="btnControl">                       
                        <button type="button" class="btn btn-success btn-xs" style="float:right;margin-left:20px;" @click="creatFence"><span class="glyphicon glyphicon-plus" aria-hidden="true" style="padding-right:5px;"></span>新建</button>
                        <button type="button" class="btn btn-default btn-xs" style="float:right;margin-left:20px;" @click="deleteFence"><span class="glyphicon glyphicon-trash" aria-hidden="true" style="padding-right:5px;"></span>删除</button>
                    </div>
                    <table>
                        <thead>
                            <tr><td>区域名称</td><td>类型</td><td>操作</td></tr>
                        </thead>
                        <tbody>
                            <tr v-for="fence in fences" :key="fence.id">
                                <td><input type="checkbox" :value="fence.fenceId" v-model="pick">{{fence.fenceName}}</td>
                                <td>{{fence.fenceType}}</td>
                                <td>
                                    <div class="dropdown">
                                        <span class="glyphicon glyphicon-cog dropdown-toggle" aria-hidden="true" id="dropdownMenu1" data-toggle="dropdown" aria-haspopup="true" aria-expanded="true" style="cursor: pointer;"></span>                                      
                                        <ul class="dropdown-menu" aria-labelledby="dropdownMenu1" style="min-width:50px;left:14px;top:0px;">
                                            <li><a style="padding:3px 15px;cursor: pointer;">编辑</a></li>
                                            <li><a style="padding:3px 15px;cursor: pointer;">关联</a></li>
                                        </ul>
                                    </div>
                                    
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div class="disply" v-if="show[1]">
                    <div class="btnControl">                       
                        <button type="button" class="btn btn-success btn-xs" style="float:right;margin-left:20px;"><span class="glyphicon glyphicon-plus" aria-hidden="true" style="padding-right:5px;"></span>新建</button>
                        <button type="button" class="btn btn-default btn-xs" style="float:right;margin-left:20px;"><span class="glyphicon glyphicon-trash" aria-hidden="true" style="padding-right:5px;"></span>删除</button>
                    </div>
                    <table>
                        <thead>
                            <tr><td><input type="checkbox">地标名称</td><td></td><td>操作</td></tr>
                        </thead>
                        <tbody>
                        </tbody>
                    </table>
                </div>
                <div class="disply" v-if="show[2]">
                    <div class="row" style="margin:0px 0px 10px 0px;">
                        <div class="col-lg-12" style="padding:0px;">
                            <div class="input-group">
                            <input type="text" class="form-control" placeholder="请输入区域名称">
                            <span class="input-group-btn">
                                <button class="btn btn-default" type="button"><span class="glyphicon glyphicon-search" aria-hidden="true"></span></button>
                            </span>
                            </div>
                        </div>
                    </div><!-- 查询 -->
                    <table>
                        <thead>
                            <tr><td><input type="checkbox">风险点名称</td></tr>
                        </thead>
                        <tbody>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
        <div id="safeMap"></div>
        <!-- 创建区域模态框 -->
        <div class="modal fade" id="safeModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <div class="modal-title" id="myModalLabel"><strong>新增区域</strong></div>
                    </div>
                    <div class="modal-body">
                        <form class="form-horizontal">
                            <div class="form-group">
                                <label for="fence_name" class="col-sm-2 control-label">区域名称</label>
                                <div class="col-sm-10">
                                <input type="text" class="form-control" id="fence_name" placeholder="请输入区域名称" required v-model="fence_name">
                                </div>
                            </div>
                            <div class="form-group">
                                <label for="description" class="col-sm-2 control-label">描述</label>
                                <div class="col-sm-10">
                                    <textarea class="form-control" rows="3" style="resize:none" v-model="description"></textarea>
                                <!-- <input type="text" class="form-control" id="description" placeholder="请输入描述信息"> -->
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="col-sm-2 control-label">类型</label>
                                <div class="col-sm-10">
                                    <label class="radio-inline">
                                        <input type="radio" name="inlineRadioOptions" id="inlineRadio1" value="platform" checked>平台围栏
                                    </label>
                                    <label class="radio-inline">
                                        <input type="radio" name="inlineRadioOptions" id="inlineRadio2" value="carport" disabled>黑车地址库 
                                    </label>
                                </div>
                            </div>
                            <div class="form-group" style="margin-bottom:0px;">
                                <label for="radius" class="col-sm-2 control-label">半径</label>
                                <div class="col-sm-8">
                                    <input type="text" class="form-control" id="radius" placeholder="200" v-model="radius">
                                </div>
                                <div class="col-sm-2" style="padding:10px 0px 0px 0px;">米</div>
                            </div>
                            <div class="form-group" v-if="radiusStatus">
                                <div class="col-sm-10 col-md-push-2" style="color:#fb3030;">半径不能小于200米</div>
                            </div>
                            <div class="form-group" style="margin-top:15px;margin-bottom:0px;">
                                <div class="col-sm-10 col-md-push-2" style="color:#fb3030">围栏：可设置进/出围栏告警，以及长时间不进/出围栏告警</div>
                            </div>
                        </form>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" @click="toDevice">保存并关联设备</button>
                        <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                    </div>
                </div>
            </div>
        </div>
        <!-- 关联设备模态框 -->
        <div class="modal fade" id="diviceModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <div class="modal-title" id="myModalLabel"><strong>关联设备</strong></div>
                    </div>
                    <div class="modal-body">
                        <div style="height:150px;width:100%;border:1px solid #ccc;overflow:auto;">
                            <div v-for="device in devices" :key="device.id"  style="padding:10px;"><input type="radio" :value="device.imei" v-model="imei"><span class="glyphicon glyphicon-hdd" aria-hidden="true" style="color:#969494;margin-left:5px;margin-right:5px;"></span>{{device.deviceName}}[{{device.imei}}]</div>
                        </div>
                        <form class="form-horizontal">
                            <div class="form-group" id="status">
                                <label class="col-sm-3 control-label">围栏告警设置:</label>                                
                                <div class="col-sm-9">
                                    <label class="checkbox-inline">
                                        <input type="checkbox" id="inlineCheckbox1" value="in" v-model="status" class="statusMsg">进  是否开启
                                    </label>
                                    <label class="checkbox-inline">
                                        <input type="checkbox" id="inlineCheckbox2" value="out" v-model="status" class="statusMsg">出  是否开启
                                    </label>
                                </div>
                            </div>
                            <div class="form-group">
                                <label class="col-sm-3 control-label">围栏停留告警:</label>                                
                                <div class="col-sm-9">
                                    <label class="checkbox-inline">
                                        <input type="checkbox" id="inlineCheckbox1" value="option1" disabled>长时间不进 <input type="text" style="width:30px;margin-right:5px;" disabled>天
                                    </label>
                                    <label class="checkbox-inline">
                                        <input type="checkbox" id="inlineCheckbox2" value="option2" disabled>长时间不出 <input type="text" style="width:30px;margin-right:5px;" disabled>天 
                                    </label>
                                </div>
                            </div>
                        </form>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" @click="save">保存</button>
                        <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                    </div>
                </div>
            </div>
        </div>
        <!-- 提示模态框 -->
        <div class="modal fade bs-example-modal-sm" id="tipsModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog modal-sm" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <div class="modal-title" id="myModalLabel"><strong>操作提示</strong></div>
                    </div>
                    <div class="modal-body">
                        {{tipsMsg}}
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" data-dismiss="modal">关闭</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import {styleJson} from '../js/mapstyle.js'
import Qs from "qs"
export default {
    name:"safe",
    data(){
        return{
            map:null,
            drawingManager:null,
            circleMarkers:[],
            labelMarkers:[],
            bgColor:["#1a2647","",""],
            color:["#fd8403","white","white"],
            show:[true,false,false],
            //添加围栏信息
            imei:"",
            lng:"",
            lat:"",
            fence_name:"",
            fence_shape:"circle",
            scale:"",
            map_type:"baidu",
            status:["in"],
            sandstatus:"",          
            radius:"",
            sendRadius:"",
            description:"",
            radiusStatus:false,
            //获取的围栏信息
            fences:[],
            //获取所有的设备信息
            devices:[],
            //选中围栏
            pick:[],
            tipsMsg:""
        }
    },
    watch:{
        pick(newValue,oldValue){
            if(newValue.length > oldValue.length){
                for(let i=0;i < this.fences.length;i++){
                    if(this.fences[i].fenceId==newValue[newValue.length-1]){
                        this.drawCircle(this.fences[i].lng,this.fences[i].lat,this.fences[i].radius,this.fences[i].scale,this.fences[i].fenceName);
                        break;
                    }
                }
            }
            if(newValue.length < oldValue.length){
                for(let i=0;i < oldValue.length;i++){
                    if(oldValue[i] != newValue[i]){
                        this.map.removeOverlay(this.circleMarkers[i]);
                        this.circleMarkers.splice(i,1);
                        this.map.removeOverlay(this.labelMarkers[i]);
                        this.labelMarkers.splice(i,1);
                        break;
                    }
                }
            }
        }
    },
    created(){
        //从store获取设备信息
        this.devices=this.$store.state.devices;
        //默认选中的第一的设备
        if(this.devices.length){
            this.imei=this.devices[0].imei;
        }      
    },
    mounted(){
        this.showMap();
        //获取所有设备围栏信息
        this.getFences();
        // this.drawCircle(113.270758,23.135589,2300);
    },
    methods:{
        changeBgColor(n){
            this.bgColor=[],
            this.bgColor[n]="#1a2647";

            this.color=["white","white","white"];
            this.color[n]="#fd8403";

            this.show=[false,false,false];
            this.show[n]=true;
        },
        showMap(){
            let map = new BMap.Map("safeMap");          // 创建地图实例  
            // let point = new BMap.Point(113.405167,22.81125);  // 大岗镇政府
            // let point = new BMap.Point(113.561854,23.131901); //黑牛
            let point = new BMap.Point(113.270758,23.135589);//广州市
            map.centerAndZoom(point, 11);  
            map.enableScrollWheelZoom(true); 
            map.addControl(new BMap.ScaleControl()); 
            map.setMapStyleV2({styleJson:styleJson});
            map.addControl(new BMap.NavigationControl({offset: new BMap.Size(20, 20)}));  
            map.addControl(new BMap.MapTypeControl({mapTypes: [BMAP_NORMAL_MAP,BMAP_HYBRID_MAP],offset: new BMap.Size(20,20),anchor:BMAP_ANCHOR_TOP_RIGHT}));   
            this.map=map;
        },
        //获取所有设备的围栏信息
        getFences(){
            if(this.devices.length){
                this.fences=[];
                for(let i=0;i<this.devices.length;i++){
                    this.$Axios.get(this.$url+"/human/getDevicePlatFence.do",{ params:{imei:this.devices[i].imei} })
                                .then((response)=>{
                                    if(response.data.status){
                                        this.fences.push(...response.data.fence);
                                    }else{
                                        this.tipsMsg="获取围栏信息失败";
                                        $('#tipsModal').modal('show');
                                    }                            
                                })
                                .catch((error)=>{
                                    if(window.console){
                                        console.log(error);
                                    }
                                })
                }
            }           
        },
        //给定圆心坐标、半径画圆;给定文本信息画文本标注
        drawCircle(lng,lat,r,scale,name){           
            let point=new BMap.Point(lng,lat);
            let circle = new BMap.Circle(point,r,{
                strokeColor:"blue",    //边线颜色。
                fillColor:"red",      //填充颜色。当参数为空时，圆形将没有填充效果。
                strokeWeight: 2,       //边线的宽度，以像素为单位。
                strokeOpacity: 0.6,    //边线透明度，取值范围0 - 1。
                fillOpacity: 0.3,      //填充的透明度，取值范围0 - 1。
                strokeStyle: "solid" //边线的样式，solid或dashed。
            });
            this.map.addOverlay(circle);
            this.map.centerAndZoom(point,scale);  
            this.circleMarkers.push(circle);

            let label=new BMap.Label(name,{
                position:point,
                color:"white"
            });
            label.setStyle({ color : "black", fontSize : "12px", backgroundColor:"white",maxWidth:"none",borderColor:"#969494"}) 
            this.map.addOverlay(label);
            this.labelMarkers.push(label);
        },
        //新建围栏
        creatFence(){
             let styleOptions = {
                strokeColor:"blue",    //边线颜色。
                fillColor:"red",      //填充颜色。当参数为空时，圆形将没有填充效果。
                strokeWeight: 2,       //边线的宽度，以像素为单位。
                strokeOpacity: 0.6,    //边线透明度，取值范围0 - 1。
                fillOpacity: 0.3,      //填充的透明度，取值范围0 - 1。
                strokeStyle: "solid" //边线的样式，solid或dashed。
            }
            //实例化鼠标绘制工具
            let drawingManager = new BMapLib.DrawingManager(this.map, {
                isOpen: false, //是否开启绘制模式
                enableDrawingTool: true, //是否显示工具栏
                drawingToolOptions: {
                    anchor: BMAP_ANCHOR_TOP_RIGHT, //位置
                    offset: new BMap.Size(70, 20), //偏离值
                    scale:0.5,
                    enableCalculate:true,
                    //绘画工具模式
                    drawingModes: [
                        // BMAP_DRAWING_MARKER,   //画点
                        BMAP_DRAWING_CIRCLE,      //画圆
                        // BMAP_DRAWING_POLYLINE, //画线
                        BMAP_DRAWING_POLYGON      //画多边形
                        // BMAP_DRAWING_RECTANGLE //画矩形
                    ]
                },
                circleOptions: styleOptions, //圆的样式
                // polylineOptions: styleOptions, //线的样式
                polygonOptions: styleOptions, //多边形的样式
                // rectangleOptions: styleOptions //矩形的样式
            }); 
            //默认画圆
            drawingManager.setDrawingMode(BMAP_DRAWING_CIRCLE);
            drawingManager.open();
            //绘制完成后派发事件
            let _this=this;
            drawingManager.addEventListener("circlecomplete", function(e, overlay) {
                $("#safeModal").modal("show");
                _this.lng=e.point.lng;
                _this.lat=e.point.lat;
                _this.map.clearOverlays(); 
                _this.circleMarkers=[];
                _this.labelMarkers=[];
                _this.pick=[];
            });
            drawingManager.addEventListener("polygoncomplete", function(e, overlay) {
                _this.tipsMsg="多边形区域创建成功";
                $('#tipsModal').modal('show');
                _this.map.clearOverlays(); 
                _this.circleMarkers=[];
                _this.labelMarkers=[];
                _this.pick=[];
            });
            this.drawingManager=drawingManager;
        },
        toDevice(){
            if(this.fence_name==""){
                $("#fence_name").css({"borderColor":"red"}).focus();
                return;
            }
            if(this.radius){
                this.sendRadius=this.radius;
            }else{
                this.sendRadius=200;           
            }    
            if(this.sendRadius < 200){
                this.radiusStatus=true;
                return;
            }
            $("#safeModal").modal("hide");
            $("#diviceModal").modal("show"); 
            $("#fence_name").css({"borderColor":"#cccccc"});   
            this.radiusStatus=false;   
        },
        save(){               
            let scale=Math.sqrt(this.sendRadius/200);
            this.scale=19-Math.round(scale);        
            if(this.status.length==0){
                $("#status").css("color","red");
                return;
            }else if(this.status.length==1){
                this.sandstatus=this.status[0];                
            }else if(this.status.length==2){
                this.sandstatus="all";
            }
            $("#diviceModal").modal("hide");
            this.drawingManager.close(); 
            $("#status").css("color","#333");
            
            this.$Axios.post(this.$url+"/human/createDevicePlatFence.do",Qs.stringify({
                             imei:this.imei,
                             fence_name:this.fence_name,
                             fence_shape:this.fence_shape,
                             status:this.sandstatus,
                             lng:this.lng,
                             lat:this.lat,
                             radius:this.sendRadius,
                             map_type:this.map_type,
                             scale:this.scale
                        }))
                        .then((response)=>{
                            
                            if(response.data.status){
                                this.getFences();
                            }else{
                                this.tipsMsg=response.data.msg;
                                $('#tipsModal').modal('show');
                            }
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
        },
        deleteFence(){
            if(this.pick.length){     
                this.$Axios.post(this.$url+"/human/delDevicePlatFence.do",Qs.stringify({
                                fence_id:this.pick.toString()
                            }))
                            .then((response)=>{
                                if(response.data.status){
                                    this.getFences();
                                    this.tipsMsg="区域围栏删除成功！";
                                    $('#tipsModal').modal('show');
                                    this.map.clearOverlays();
                                    this.circleMarkers=[];
                                    this.labelMarkers=[];
                                    this.pick=[];
                                }else{
                                    this.tipsMsg="区域围栏删除失败！";
                                    $('#tipsModal').modal('show');
                                }
                                
                            })
                            .catch((error)=>{
                                if(window.console){
                                    console.log(error);
                                }
                            })
            }else{
                this.tipsMsg="请选择要删除的区域";
                $('#tipsModal').modal('show');
            }
        }
        
    }

}
</script>
<style lang="less" scoped>
#safe{
    width: 100%;
    height: 100%;
    #safeNav{
        width: 350px;
        height: calc(100% - 60px);
        position:absolute;
        left: 0px;
        // background-color: #070247;
        background-color: #1b1a1a;
        background-color: #031830;
        color:aliceblue;
        #navList{
            width: 100%;
            height: 50px;
            text-align: center;   
            ul{
                height: 100%;
                width: 100%;
                margin:0px;
                list-style-type: none;
                font-weight: bolder;
                li{
                    width: 33.3%;
                    height: 40px;
                    margin-top: 10px;
                    padding:10px;
                    // border-radius: 5px;
                    // border:1px solid #1b1a1a;
                    border-top-left-radius:5px;
                    border-top-right-radius: 5px;
                    float:left;
                    // padding-top: 15px;
                    cursor: pointer;                    
                }
            }
        }
        #dispalyList{
            width: 100%;
            height: calc(100% - 50px);
            background-color: #1a2647;
            background-color: #1a2647;
            padding:5%;
            overflow: auto;
            .disply{
                width: 100%;
                height: 100%;
                .btnControl{
                    width: 100%;
                    height: 35px;
                    padding:5px;
                }
                table{
                    width: 100%;
                    tr{
                        border-bottom: 1px solid #969494;
                    }
                    td{
                        padding:5px;
                    }
                    thead{
                        font-weight: bold;
                        background-color: #283966;
                    }
                    tbody{
                        background-color: #444444;
                        background-color: #2e4177;
                    }
                }
            }
            
        }
    }
    #safeMap{
        width:calc(100% - 350px);
        height: 100%;
        float:right;
        background-color: white;
        color: #666564;
        overflow: hidden;
    }
    .modal-dialog{
        top:20%;
    }
}
</style>





