<template>
    <div id="location">
        <!-- 顶栏 -->
        <div id="locationNav">
            <div style="float:left;height:100%;width:90px;padding:5px;"><img src="../../assets/wifi.png" alt="行驶" height="100%"><span>行驶({{move}})</span></div>
            <div style="float:left;height:100%;width:90px;padding:5px;"><img src="../../assets/stop.png" alt="静止" height="100%"><span>静止({{stop}})</span></div>
            <div style="float:left;height:100%;width:90px;padding:5px;"><img src="../../assets/outLine.png" alt="离线" height="100%"><span>离线({{outLine}})</span></div>
            <div style="float:left;height:100%;width:90px;padding:5px;"><img src="../../assets/inactive.png" alt="未激活" height="100%"><span>未激活({{inactive}})</span></div>
            <div style="float:right;height:100%;width:160px;padding:5px;">
                刷新时间：
                <select name="refreshTime" v-model="selectTime">
                    <option value="60" checked>1分钟</option>
                    <option value="120">2分钟</option>
                    <option value="300">5分钟</option>
                    <option value="600">10分钟</option>
                </select>
            </div>
        </div>
        <!-- 刷新时间(右上) -->
        <div id="refreshTime"><span style="color:#13ad03;font-weight:bold;">{{refreshTime}}</span>秒后刷新</div>       
        <!-- 地图 -->
        <div id="map"></div>    
        <!-- 信息提示模态框 -->
        <div class="modal fade bs-example-modal-sm" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog modal-sm" role="document" id="modal">
                <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <div class="modal-title" id="myModalLabel"><strong>操作提示</strong></div>
                </div>
                <div class="modal-body">
                    {{msg}}
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-primary" data-dismiss="modal">关闭</button>
                </div>
                </div>
            </div>
        </div>  
        <!-- 视频连线窗口 -->
        <div id="videoShow" v-if="show">
            <div id="x"><div style="height:100%;width:calc(100% - 30px);float:left;"  @mouseover="over"></div><img src="../../assets/x1.png" alt="x" @click="show=false;"></div>
            <div id="videoContainer">
                <div id="msg">
                    <div v-html="deviceMsg" style="width:100%;height:88%;"></div>
                    <!-- <div style="height:40%;width:100%;"><img :src="photoUrl" alt="照片" style="height:100%;margin-left:30%;"></div>     
                    <div class="worker">{{name}}</div>
                    <div class="worker">{{sex}}</div>
                    <div class="worker">{{age}}</div>
                    <div class="worker">{{community}}</div>
                    <div class="worker">{{street}}</div> -->
                    <div id="imags">
                        <span><img src="../../assets/voice2.png" alt="voice" style="margin-left:10%;"></span>
                        <span><img src="../../assets/micro1.png" alt="micro" style="margin-left:14%;"></span>
                        <span><img src="../../assets/phone1.png" alt="phone" style="margin-left:14%;"></span>  
                    </div>
                </div>
                <div id="onLine" v-html="onLineShow"></div>          
            </div>
        </div>
        <!-- 积水监测视频 -->
        <div id="waterCamera" v-if="cameraShow">
            <img src="../../assets/x1.png" id="hide" alt="x" @click="cameraShow=false;">
            <video src="../../assets/cameras/camera1.mp4" controls="controls" autoplay="autoplay" loop="loop"></video>
        </div> 
        <!-- 积水监测历史数据 -->
        <!-- <div id="waterData" v-if="cameraShow">

        </div> -->
    </div>
</template>
<script>
import {styleJson} from '../../js/mapstyle.js'
export default {
    name:'location',
    data(){
        return{
            map:null,
            msg:'',
            locationDevice:[],
            //视频连线窗口信息
            show:false,//默认不显示
            street:'',
            deviceMsg:'',
            onLineShow:'',
            //设备状态信息
            move:0,
            stop:1,
            outLine:1,
            inactive:1,
            //刷新时间
            selectTime:60,
            refreshTime:60,
            //环卫工图标
            workerMarkers:[],
            //环卫车图标
            carMarkers:[],
            //环卫机器人
            robotMarkers:[],
            //智能垃圾桶图标
            trashMarkers:[], 
            //积水监测图标
            cameraMarkers:[],
            cameraShow:false      
        }
    },
    computed:{
        //获取所有要在地图上显示的环卫工的IMEI
        getLocationImeis(){
            return this.$store.state.workers.locationImeis;
        },
        //获取要显示的环卫车IMEI
        getCarImeis(){
            return this.$store.state.cars.locationImeis;
        },
         //获取要显示的环卫机器人IMEI
         getRobotImeis(){
             return this.$store.state.robots.locationImeis;
         },
        //获取所有要在地图上显示的智能垃圾桶数据
        trash(){
            return this.$store.state.trash;
        },
        getTrash(){
            return this.$store.state.sendtrash;
        },
        //获取所有要在地图上显示的积水监控设备数据
        getWaterCamera(){
            return this.$store.state.sendCamera;
        }
    },
    watch:{   
        //环卫工的IMEI有变动就更新地图上的图标和信息
        getLocationImeis(newValue,oldValue){
            if(newValue.length > oldValue.length){
                this.showWorkerOnMap(newValue[newValue.length-1]);
            }else{
                for(let i=0;i < oldValue.length;i++){
                    if(oldValue[i] != newValue[i]){
                        this.map.removeOverlay(this.workerMarkers[i]);
                        this.workerMarkers.splice(i,1);
                        break;
                    }
                }
            }
        },
        getCarImeis(newValue,oldValue){
            if(newValue.length > oldValue.length){
                this.showCarOnMap(newValue[newValue.length-1]);
            }else{
                for(let i=0;i < oldValue.length;i++){
                    if(oldValue[i] != newValue[i]){
                        this.map.removeOverlay(this.carMarkers[i]);
                        this.carMarkers.splice(i,1);
                        break;
                    }
                }
            }
        },
        getRobotImeis(newValue,oldValue){
            if(newValue.length > oldValue.length){
                this.showRobotOnMap(newValue[newValue.length-1]);
            }else{
                for(let i=0;i < oldValue.length;i++){
                    if(oldValue[i] != newValue[i]){
                        this.map.removeOverlay(this.robotMarkers[i]);
                        this.robotMarkers.splice(i,1);
                        break;
                    }
                }
            }
        },
        //有变动则更新地图上的垃圾桶信息
        getTrash(newValue,oldValue){
            if(newValue.length > oldValue.length){
                this.showTrashOnMap(newValue[newValue.length-1]);
            }else{
                for(let i=0;i < oldValue.length;i++){
                    if(oldValue[i] != newValue[i]){
                        this.map.removeOverlay(this.trashMarkers[i]);
                        this.trashMarkers.splice(i,1);
                        break;
                    }
                }
            }
        },
        //有变动则更新地图上的积水监测设备信息
        getWaterCamera(newValue,oldValue){
            if(newValue.length > oldValue.length){
                this.showCameraOnMap(newValue[newValue.length-1]);
            }else{
                for(let i=0;i < oldValue.length;i++){
                    if(oldValue[i] != newValue[i]){
                        this.map.removeOverlay(this.cameraMarkers[i]);
                        this.cameraMarkers.splice(i,1);
                        break;
                    }
                }
            }
        },
        selectTime(newValue){
            this.refreshTime=this.selectTime;
        },
    },
    mounted(){
        this.showMap();
        //在未刷新请款下返回控制台页面，获取已经选中的环卫工状态并在地图显示
        if(this.getLocationImeis.length != 0){
            // this.getLocationDevice(this.getLocationImeis);
            for(let i=0;i<this.getLocationImeis.length;i++){
                this.showWorkerOnMap(this.getLocationImeis[i]);
            }
        }  
        //在未刷新请款下返回控制台页面，获取已经选中的环卫车状态并在地图显示  
        if(this.getCarImeis.length !=0){
            for(let i=0;i<this.getCarImeis.length;i++){
                this.showCarOnMap(this.getCarImeis[i]);
            }
        }  
        //在未刷新请款下返回控制台页面，获取已经选中的环卫机器人状态并在地图显示
        if(this.getRobotImeis.length !=0){
            for(let i=0;i<this.getRobotImeis.length;i++){
                this.showRobotOnMap(this.getRobotImeis[i]);
            }
        }
        //在未刷新请款下返回控制台页面，获取已经选中的垃圾桶状态并在地图显示
        if(this.getTrash.length){
            for(let i=0;i<this.getTrash.length;i++){
                this.showTrashOnMap(this.getTrash[i]);
            }
        }
        //在未刷新请款下返回控制台页面，获取已经选中的积水监测状态并在地图显示
        if(this.getWaterCamera.length){
            for(let i=0;i<this.getWaterCamera.length;i++){
                this.showCameraOnMap(this.getWaterCamera[i]);
            }
        }
        //开启刷新
        setInterval(()=>{
            this.refreshTime = --this.refreshTime;
            if(this.refreshTime<0){
                this.refreshTime=this.selectTime;
                
            }
        },1000)
        
    },
    methods:{
        showMap(){
            let map = new BMap.Map("map");          // 创建地图实例  
            // let point = new BMap.Point(113.405167,22.81125);  // 大岗镇政府
            // let point = new BMap.Point(113.561854,23.131901); //黑牛
            let point = new BMap.Point(113.270758,23.135589);//广州市
            map.centerAndZoom(point, 11);  
            map.enableScrollWheelZoom(true);  
            map.setMapStyleV2({styleJson:styleJson});
            map.addControl(new BMap.NavigationControl({offset: new BMap.Size(20, 70)}));  
            map.addControl(new BMap.MapTypeControl({mapTypes: [BMAP_NORMAL_MAP,BMAP_HYBRID_MAP],offset: new BMap.Size(20,70),anchor:BMAP_ANCHOR_TOP_RIGHT}));  
            this.map=map;
        },
        showWorkerOnMap(imei){
             this.$Axios.get(this.$url+'/human/getGPSInfo.do',{params:{imeis:imei,t: Date.parse(new Date())}})
                        .then((response)=>{
                            // if(window.console){
                            //     console.log(response);
                            // }
                            if(response.data.list){
                                let worker=response.data.list[0];
                                let point=new BMap.Point(worker.lng,worker.lat);
                                let myIcon = new BMap.Icon(require("../../assets/worker3.png"), new BMap.Size(28,28));      
                                let marker = new BMap.Marker(point, {icon: myIcon});    
                                this.map.addOverlay(marker); 
                                this.map.centerAndZoom(point, 18); 
                                this.workerMarkers.push(marker); 
                                if(!worker.speed){worker.speed="暂无速度"}
                                let sContent=`
                                            <div style="line-height:180%;">
                                                <div>IEMI: ${worker.imei}</div>
                                                <div>用户姓名:${worker.username}<div>
                                                <div>速度:${worker.speed}</div>
                                                <div>定位时间:${worker.gpsTime}(${worker.posType}定位)</div>
                                                <div>通讯时间:${worker.hbTime}</div>
                                                <div style="color:red;text-align:center;">双击图标进行视频连线</div>
                                            </div>`
                                let InfoWindow = new BMapLib.SearchInfoWindow(this.map, sContent,{
                                    title: worker.deviceName, //标题
                                    width: 270, //宽度
                                    height: 130, //高度
                                    panel : "panel", //检索结果面板
                                    enableAutoPan : true, //自动平移
                                    enableSendToPhone: false,
                                    searchTypes :[
                                    ]
                                });
                                InfoWindow.open(point); 
                                marker.addEventListener("click",()=>{    
                                    InfoWindow.open(point); 
                                }); 
                                marker.addEventListener("dblclick",()=>{
                                    let photoUrl;
                                    try{
                                        photoUrl=require("../../assets/workers/"+worker.username+".jpg");
                                    }catch(error){
                                        photoUrl="";
                                    }
                                    //位置信息
                                    let geoc = new BMap.Geocoder();  
                                    geoc.getLocation(point,(rs)=>{
                                        let addComp = rs.addressComponents;
                                        let position=addComp.province + addComp.city + addComp.district + addComp.street + addComp.streetNumber;
                                        this.deviceMsg=`
                                                        <div style="height:40%;width:100%;"><img src=${photoUrl} alt="照片" style="height:100%;margin-left:30%;"></div>     
                                                        <div class="worker">姓名：${worker.username}</div>
                                                        <div class="worker">性别：${worker.sex}</div>
                                                        <div class="worker">年龄：${worker.age}</div>
                                                        <div class="worker">作业社区：${worker.community}</div>
                                                        <div class="worker">位置：${position}</div>`;
                                    
                                    });
                                    this.onLineShow=`
                                                <object type="application/x-vlc-plugin" id="vlc_1" events="True" codebase="axvlc.cab">
                                                    <param name="mrl" value=${worker.rtsp} />
                                                    <param name="volume" value="50" />
                                                    <param name="autoplay" value="true" />
                                                    <param name="loop" value="false" />
                                                    <param name="fullscreen" value="true" />
                                                </object>`;
                                    this.show=true;
                                }) 
                            }else{
                                this.msg="定位数据为空";
                                $('#myModal').modal('show');
                            }

                        })
        },
        //视频连线窗口拖动
        over(event){
            event.currentTarget.style.cursor="move";
            $(event.currentTarget).mousedown((e)=>{         
                let a=e.pageX-$("#videoShow").offset().left;//鼠标按下当时距离视频最最左边距离
                let b=e.pageY-$("#videoShow").offset().top;//鼠标按下当时距离视频最最上边距离
                $(document).mousemove((e)=>{    
                    let X=e.pageX-a;
                    let Y=e.pageY-b;
                    if(X<0){
                        X=0;
                    }else if(X>($(document).width()-$("#videoShow").width())){
                        X=$(document).width()-$("#videoShow").width();
                    }
                    if(Y<0){
                        Y=0;
                    }else if(Y>($(document).height()-$("#videoShow").height())){
                        Y=$(document).height()-$("#videoShow").height()-5;
                    }
                    $("#videoShow").css({"top":Y+"px","left":X+"px"});
                });
                $(document).mouseup((e)=>{
                    $(document).unbind("mousemove");//停止移动                            
                });  
            })
            
        },
        showCarOnMap(carImei){
            this.$Axios.get(this.$url+"/human/getVehicleGPSInfo.do",{params:{imeis:carImei,t: Date.parse(new Date())}})
                        .then((response)=>{
                            // if(window.console){
                            //     console.log(response);
                            // }
                            if(response.data.list){
                                let car=response.data.list[0];
                                let point=new BMap.Point(car.lng,car.lat);
                                let myIcon = new BMap.Icon(require("../../assets/car2.png"), new BMap.Size(50,21));      
                                let marker = new BMap.Marker(point, {icon: myIcon});    
                                this.map.addOverlay(marker); 
                                this.map.centerAndZoom(point, 18); 
                                this.carMarkers.push(marker);
                                let sContent=`
                                            <div style="line-height:180%;">
                                                <div>IEMI: ${car.imei}</div>
                                                <div>车牌号:${car.sim}</div>
                                                <div>驾驶员:${car.username}<div>
                                                <div>定位时间:${car.gpsTime}(${car.posType}定位)</div>
                                                <div>通讯时间:${car.hbTime}</div>
                                                <div style="color:red;text-align:center;">双击图标进行视频连线</div>
                                            </div>`
                                let InfoWindow = new BMapLib.SearchInfoWindow(this.map, sContent,{
                                    title: car.deviceName, //标题
                                    width: 270, //宽度
                                    height: 130, //高度
                                    panel : "panel", //检索结果面板
                                    enableAutoPan : true, //自动平移
                                    enableSendToPhone: false,
                                    searchTypes :[
                                    ]
                                });
                                InfoWindow.open(point); 
                                marker.addEventListener("click",()=>{    
                                    InfoWindow.open(point); 
                                });
                                marker.addEventListener("dblclick",()=>{
                                    let photoUrl=require("../../assets/car3.png");
                                    let geoc = new BMap.Geocoder();  
                                    geoc.getLocation(point,(rs)=>{
                                        let addComp = rs.addressComponents;
                                        let position=addComp.province + addComp.city + addComp.district + addComp.street + addComp.streetNumber;
                                        this.deviceMsg=`
                                                <div style="height:40%;width:100%;"><img src=${photoUrl} alt="照片" style="height:100%;margin-left:30%;"></div>    
                                                <div class="worker">车牌号：${car.sim}</div> 
                                                <div class="worker">驾驶员：${car.username}</div>
                                                <div class="worker">性别：${car.sex}</div>
                                                <div class="worker">年龄：${car.age}</div>
                                                <div class="worker">作业社区：${car.community}</div>
                                                <div class="worker">位置：${position}</div>`;
                                    });
                                    this.onLineShow=`
                                                <object type="application/x-vlc-plugin" id="vlc_1" events="True" codebase="axvlc.cab">
                                                    <param name="mrl" value=${car.rtsp} />
                                                    <param name="volume" value="50" />
                                                    <param name="autoplay" value="true" />
                                                    <param name="loop" value="false" />
                                                    <param name="fullscreen" value="true" />
                                                </object>`;
                                    this.show=true;
                                }) 
                            }else{
                                this.msg="定位数据为空";
                                $('#myModal').modal('show');
                            }
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
        },
        showRobotOnMap(robotImei){
            this.$Axios.get(this.$url+"/human/getRobotGPSInfo.do",{params:{imeis:robotImei}})
                        .then((response)=>{
                            // if(window.console){
                            //     console.log(response);
                            // }
                            if(response.data.list){
                                let robot=response.data.list[0];
                                let point=new BMap.Point(robot.lng,robot.lat);
                                let myIcon = new BMap.Icon(require("../../assets/robot4.png"), new BMap.Size(39,28));      
                                let marker = new BMap.Marker(point, {icon: myIcon});    
                                this.map.addOverlay(marker); 
                                this.map.centerAndZoom(point, 18);
                                this.robotMarkers.push(marker); 
                                if(!robot.speed){robot.speed="暂无速度"}
                                let sContent=`
                                            <div style="line-height:180%;">
                                                <div>IEMI: ${robot.imei}</div>
                                                <div>速度：${robot.speed}</div>
                                                <div>定位时间:${robot.gpsTime}(${robot.posType}定位)</div>
                                                <div>通讯时间:${robot.hbTime}</div>
                                                <div>设备状态：${robot.status}</div>
                                                <div style="color:red;text-align:center;">双击图标进行视频连线</div>
                                            </div>`
                                let InfoWindow = new BMapLib.SearchInfoWindow(this.map, sContent,{
                                    title: robot.deviceName, //标题
                                    width: 270, //宽度
                                    height: 130, //高度
                                    panel : "panel", //检索结果面板
                                    enableAutoPan : true, //自动平移
                                    enableSendToPhone: false,
                                    searchTypes :[
                                    ]
                                });
                                InfoWindow.open(point); 
                                marker.addEventListener("click",()=>{    
                                    InfoWindow.open(point); 
                                }); 
                                marker.addEventListener("dblclick",()=>{
                                    let photoUrl=require("../../assets/robot5.png");   
                                    let geoc = new BMap.Geocoder();  
                                    geoc.getLocation(point,(rs)=>{
                                        let addComp = rs.addressComponents;
                                        let position=addComp.province + addComp.city + addComp.district + addComp.street + addComp.streetNumber;
                                        this.deviceMsg=`
                                                    <div style="height:40%;width:100%;"><img src=${photoUrl} alt="照片" style="height:100%;margin-left:30%;"></div>
                                                    <div class="worker">设备号：${robot.imei}</div>
                                                    <div class="worker">设备名称：${robot.deviceName}</div>
                                                    <div class="worker">设备类型：${robot.mcType}</div>
                                                    <div class="worker">状态：${robot.status}</div>
                                                    <div class="worker">位置：${position}</div>`;
                                    }); 
                                    this.onLineShow=`
                                                <object type="application/x-vlc-plugin" id="vlc_1" events="True" codebase="axvlc.cab">
                                                    <param name="mrl" value=${robot.rtsp} />
                                                    <param name="volume" value="50" />
                                                    <param name="autoplay" value="true" />
                                                    <param name="loop" value="false" />
                                                    <param name="fullscreen" value="true" />
                                                </object>`;
                                    this.show=true;
                                }) 
                            }else{
                                this.msg="定位数据为空";
                                $('#myModal').modal('show');
                            }
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
        },
        showTrashOnMap(trash){
            // if(window.console){
            //     console.log(trash);
            // }
            let point=new BMap.Point(trash.longitude,trash.latitude);
            let trashUrl=require("../../assets/trash1.png");
            // if(trash.trashstatus=="溢满"){
            //     trashUrl=require("../../assets/trash4.png");
            // }else if(trash.trashstatus=="倾倒"){
            //     trashUrl=require("../../assets/trash5.png");
            // }else if(trash.trashstatus=="起火"){
            //     trashUrl=require("../../assets/trash3.png");
            // }else if(trash.trashstatus=="低电量"){
            //     trashUrl=require("../../assets/trash2.png");
            // }
            let myIcon = new BMap.Icon(trashUrl, new BMap.Size(28,35));      
            let marker = new BMap.Marker(point, {icon: myIcon});    
            this.map.addOverlay(marker); 
            this.map.centerAndZoom(point, 18);  
            this.trashMarkers.push(marker);
            let sContent=`
                        <ul style="margin-left:10px;line-height:180%;">
                            <li>IMEI:${trash.imei}</li>
                            <li>所属:${trash.city+trash.area+trash.road}</li>
                            <li>点位:${trash.trashname}</li>
                            <li>空气高度:${trash.air_height}</li>
                            <li>状态:${trash.trashstatus}</li>
                            <div style="color:red;text-align:center;">双击图标进行视频连线</div>
                        </ul>`
            let InfoWindow = new BMapLib.SearchInfoWindow(this.map, sContent,{
                title:"智能垃圾桶信息", //标题
                width: 270, //宽度
                height: 130, //高度
                panel : "panel", //检索结果面板
                enableAutoPan : true, //自动平移
                enableSendToPhone: false,
                searchTypes :[
                ]
            });
            InfoWindow.open(point); 
            marker.addEventListener("click",()=>{    
                InfoWindow.open(point); 
            }); 
            marker.addEventListener("dblclick",()=>{
                let photoUrl=require("../../assets/trash6.png");    
                let geoc = new BMap.Geocoder();  
                geoc.getLocation(point,(rs)=>{
                    let addComp = rs.addressComponents;
                    let position=addComp.province + addComp.city + addComp.district + addComp.street + addComp.streetNumber;
                    this.deviceMsg=`
                                <div style="height:40%;width:100%;"><img src=${photoUrl} alt="照片" style="height:100%;margin-left:30%;"></div>
                                <div class="worker">设备号：${trash.imei}</div>
                                <div class="worker">设备名称：${trash.trashname}</div>
                                <div class="worker">所属：${trash.area+trash.road}</div>
                                <div class="worker">状态：${trash.online}</div>
                                <div class="worker">位置：${position}</div>`;
                }); 
                this.onLineShow=`
                            <object type="application/x-vlc-plugin" id="vlc_1" events="True" codebase="axvlc.cab">
                                <param name="mrl" value=${trash.rtsp} />
                                <param name="volume" value="50" />
                                <param name="autoplay" value="true" />
                                <param name="loop" value="false" />
                                <param name="fullscreen" value="true" />
                            </object>`;
                this.show=true;
            }) 
            
        },
        showCameraOnMap(camera){
            let point=new BMap.Point(camera.lon,camera.lat);
            let myIcon = new BMap.Icon(require("../../assets/camera2.png"), new BMap.Size(39,28));      
            let marker = new BMap.Marker(point, {icon: myIcon});    
            this.map.addOverlay(marker); 
            this.map.centerAndZoom(point, 18); 
            this.cameraMarkers.push(marker);
            let sContent=`
                        <ul style="margin-left:10px;line-height:180%;">
                            <li>道路：${camera.position}</li>
                            <li>积水深度：${camera.ponding_thickness}cm</li>
                            <li>监测时间：${camera.insert_time_now}</li>
                            <li>设备状态：${camera.online_state}</li>
                            <div style="color:red;text-align:center;">双击图标进行视频连线</div>
                        </ul>`
            let InfoWindow = new BMapLib.SearchInfoWindow(this.map, sContent,{
                title:"道路积水监测", //标题
                width: 270, //宽度
                height: 130, //高度
                panel : "panel", //检索结果面板
                enableAutoPan : true, //自动平移
                enableSendToPhone: false,
                searchTypes :[
                ]
            });
            InfoWindow.open(point); 
            marker.addEventListener("click",()=>{    
                InfoWindow.open(point); 
            }); 
            marker.addEventListener("dblclick",()=>{
                let photoUrl=require("../../assets/camera3.png");   
                let geoc = new BMap.Geocoder();  
                geoc.getLocation(point,(rs)=>{
                    let addComp = rs.addressComponents;
                    let position=addComp.province + addComp.city + addComp.district + addComp.street + addComp.streetNumber;
                }); 
                this.deviceMsg=`
                                <div style="height:40%;width:100%;"><img src=${photoUrl} alt="照片" style="height:100%;margin-left:30%;"></div>
                                <div class="worker">设备号：${camera.imei}</div>
                                <div class="worker">状态：${camera.online_state}</div>
                                <div class="worker">积水深度：${camera.ponding_thickness}cm</div>
                                <div class="worker">道路：${camera.position}</div>`;
                this.onLineShow=`
                            <object type="application/x-vlc-plugin" id="vlc_1" events="True" codebase="axvlc.cab">
                                <param name="mrl" value=${camera.rtsp} />
                                <param name="volume" value="50" />
                                <param name="autoplay" value="true" />
                                <param name="loop" value="false" />
                                <param name="fullscreen" value="true" />
                            </object>`;
                this.show=true;
            }) 

          
        },
        getHistory(camera){
              this.$Axios.get(this.$url+"/human/getPondingInfo.do",{params:{id:camera.id,history:1}})
                        .then((response)=>{
                            if(window.console){
                                console.log(response);
                            }
                            if(response.data.list){
                                let cameras=response.data.list;
                                let history=null;
                                for(let i=0;i<cameras.length;i++){
                                    if(cameras[i].id==camera.id){
                                        history=cameras[i].history;
                                        break;
                                    }
                                }
                                console.log(history);
                                document.getElementById('waterData').innerHTML="";
                                let myChart = this.$echarts.init(document.getElementById('waterData'));
                                myChart.setOption({
                                    xAxis: {
                                        type: 'category',
                                        boundaryGap: false,
                                        data: [history[0].ponding_time_now, history[1].ponding_time_now, history[2].ponding_time_now, history[3].ponding_time_now, history[4].ponding_time_now, history[5].ponding_time_now, history[6].ponding_time_now],
                                        axisLine:{
                                            lineStyle:{
                                                color:"white",
                                                width:2,//这里是为了突出显示加上的
                                            }
                                        }
                                    },
                                    yAxis: {
                                        type: 'value',
                                        axisLine:{
                                            lineStyle:{
                                                color:"white",
                                                width:2,//这里是为了突出显示加上的
                                            }
                                        }
                                    },
                                    series: [{
                                        data: [history[0].ponding_thickness, history[1].ponding_thickness, history[2].ponding_thickness,history[3].ponding_thickness,history[4].ponding_thickness,history[5].ponding_thickness,history[6].ponding_thickness],
                                        type: 'line',
                                        areaStyle: {}
                                    }]
                                });

                            }else{
                                this.msg="积水监测数据为空";
                                $('#myModal').modal('show');
                            }
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
        }
        
    }
}
</script>
<style lang="less">
#location{
    height: 100%;
    width: 100%;
    #locationNav{
        height:40px;
        width: calc(100% - 350px);
        position: absolute;
        z-index: 2;
        background-color: rgb(35, 48, 90);
        color: white;
        padding:5px;
        select{
            color:#666564;
        }
    }
    #refreshTime{
        position: absolute;
        z-index: 2;
        background-color: white;
        top:190px;
        left:430px;
        color: #666564;
        padding:2px 5px;
    }
    #map{
        height: 100%;
        width: 100%;
    }
    #modal{
        margin-top: 210px;
    }
    #videoShow{
        width: 700px;
        height: 300px;
        position:absolute;
        bottom: 5px;
        right: 2px;
        z-index: 999;
        // background-color: #040224;
        background-color:#282729;
        background-color: #1a2647;
        #x{
            height: 25px;
            width: 100%;
            img{
                float: right;
                margin-right: 10px;
                margin-top: 5px;
                &:hover{
                    cursor: pointer;
                }
            }
        }
        #videoContainer{
            width: 100%;
            height: calc(100% - 25px);
            // background-color: #030116;
            background-color:#1b1a1a;
            background-color: #202e55;
            color: aliceblue;
            #msg{
                width: 30%;
                height: 100%;
                float: left;
                padding:10px;
                #imags{
                    height: 25px;
                    width: 100%;
                    margin-top:3%;
                    span{
                         width: 33.3%;
                        width: 33.3%;
                        img{
                            // width: 33.3%;
                            height: 100%;
                            float: left;
                            &:hover{
                                cursor: pointer;
                            }
                        }
                    }
                    
                }
            }
            #onLine{
                width: 70%;
                height: 100%;
                float: left;
                object{
                    width: 100% !important; 
                    height: 100% !important;
                }
            }
           
        }
        
    }
    #waterCamera{
        width: 500px;
        height: 300px;
        position:absolute;
        top: 160px;
        right: 2px;
        z-index: 999;
        background-color:#282729;
        video{
            width: 100%;
            height:100%;            
        }
        #hide{
                position:absolute;
                z-index: 1000;
                top:20px;
                right: 10px;
                cursor: pointer;
            }
    }
    #waterData{
        width: 500px;
        height: 300px;
        position:absolute;
        top: 160px;
        left: 351px;
        z-index: 999;
        background-color:#282729;
    }
}
</style>






