<template>
    <div id="carHZ">
        <div id="carTitle">环卫车驾驶员管理</div>
        <div class="box">   
            <button type="button" class="btn btn-primary btn-sm" data-toggle="modal" data-target="#carModal">新增成员</button>  
            <span id="refresh" class="glyphicon glyphicon-repeat" aria-hidden="true" @click="getDrivers"></span>
        </div>
        <table>
            <caption>驾驶员信息</caption>
            <thead>           
                <tr>
                    <td>驾驶员</td><td>性别</td><td>年龄</td><td>驾龄(年)</td><td>联系方式</td><td>作业社区</td><td>操作</td>
                </tr>
            </thead>
            <tbody>
                <tr v-for="driver in drivers" v-bind:key="driver.id">
                   <td>{{driver.username}}</td><td>{{driver.sex}}</td><td>{{driver.age}}</td><td>{{driver.lon}}</td><td>{{driver.phone}}</td><td>{{driver.community}}</td><td style="max-width:70px;"><button type="button"  class="btn btn-sm btn-default" @click="editDriver(driver)">编辑</button><button type="button"  class="btn btn-sm btn-primary" @click="deleteDriver(driver.id)">删除</button></td>
                </tr>
            </tbody>
        </table>
        <!-- 新增成员信息填写模态框 -->
        <div class="modal fade" id="carModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <h4 class="modal-title" id="myModalLabel" style="font-weight:bold;">新增驾驶员</h4>
                </div>
                <div class="modal-body">
                    <form id="workerForm">
                        <div class="form-group">
                            <label for="name">姓名</label>
                            <input type="text" name="username" class="form-control" id="name" v-model="username">
                        </div>
                        <div class="form-group">
                            <label>性别</label><br>
                            <label class="radio-inline">
                                <input type="radio" name="inlineRadioOptions" id="1" value="1" v-model="sex">男
                                </label>
                                <label class="radio-inline">
                                <input type="radio" name="inlineRadioOptions" id="0" value="0" v-model="sex">女
                            </label>
                        </div>                      
                        <div class="form-group">
                            <label for="age">年龄</label>
                            <input type="text" name="age" class="form-control" id="age" v-model="age">
                        </div>
                        <div class="form-group">
                            <label for="age">驾龄</label>
                            <input type="text" name="age" class="form-control" id="age" v-model="driverAge">
                        </div>
                        <div class="form-group">
                            <label for="phone">联系方式</label>
                            <input type="text" name="phone" class="form-control" id="phone" v-model="phone">
                        </div>
                        <div class="form-group">
                            <label>作业社区</label><br>
                            <select class="form-control" v-model="community">
                                <option v-for=" street in streetNames" :key="street.id" :value="street.streetName">{{street.streetName}}</option>                                
                            </select>
                        </div>
                        <!-- <div class="form-group">
                            <label for="rtsp">流地址</label>
                            <input type="text" name="rtsp" class="form-control" id="rtsp" v-model="rtspUrl">
                        </div> -->
                    </form>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-primary" @click="save">保存</button>
                </div>
                </div>
            </div>
        </div>
        <!-- 编辑 -->
        <div class="modal fade" id="editCarModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                    <h4 class="modal-title" id="myModalLabel" style="font-weight:bold;">新增驾驶员</h4>
                </div>
                <div class="modal-body">
                    <form id="workerForm">
                        <div class="form-group">
                            <label for="name">姓名</label>
                            <input type="text" name="username" class="form-control" id="name" v-model="editUserName">
                        </div>
                        <div class="form-group">
                            <label>性别</label><br>
                            <label class="radio-inline">
                                <input type="radio" name="inlineRadioOptions" id="1" value="1" v-model="editSex">男
                                </label>
                                <label class="radio-inline">
                                <input type="radio" name="inlineRadioOptions" id="0" value="0" v-model="editSex">女
                            </label>
                        </div>                      
                        <div class="form-group">
                            <label for="age">年龄</label>
                            <input type="text" name="age" class="form-control" id="age" v-model="editAge">
                        </div>
                        <div class="form-group">
                            <label for="age">驾龄</label>
                            <input type="text" name="age" class="form-control" id="age" v-model="editDriverAge">
                        </div>
                        <div class="form-group">
                            <label for="phone">联系方式</label>
                            <input type="text" name="phone" class="form-control" id="phone" v-model="editPhone">
                        </div>
                        <div class="form-group">
                            <label>作业社区</label><br>
                            <select class="form-control" v-model="editCommunity">
                                <option v-for=" street in streetNames" :key="street.id" :value="street.streetName">{{street.streetName}}</option>                                
                            </select>
                        </div>
                        <!-- <div class="form-group">
                            <label for="rtsp">流地址</label>
                            <input type="text" name="rtsp" class="form-control" id="rtsp" v-model="rtspUrl">
                        </div> -->
                    </form>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-primary" @click="saveEditDriver">保存</button>
                </div>
                </div>
            </div>
        </div>
        <!-- 提示模态框 -->
        <div class="modal fade bs-example-modal-sm" id="tipsModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
            <div class="modal-dialog modal-sm" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true">&times;</span></button>
                        <div class="modal-title" id="myModalLabel"><strong>操作提示</strong></div>
                    </div>
                    <div class="modal-body">
                        {{msg}}
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-primary" data-dismiss="modal">关闭</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:'carHZ',
    data(){
        return{
            streetNames:[],
            drivers:[],
            //新增驾驶员信息
            username:'',
            sex:'',
            age:'',
            driverAge:'',
            phone:'',
            community:'',
            rtspUrl:'',
            //编辑信息
            editID:'',
            editUserName:'',
            editSex:'',
            editAge:'',
            editDriverAge:'',
            editPhone:'',
            editCommunity:'',
            msg:''
        }
    },
    created(){
        this.getDrivers();
        this.getStreetNames();
    },
    computed:{
        num(){
            return this.$store.state.cars.num;
        },
        ownNum(){
            return this.$store.state.cars.ownNum;
        },
        lackNum(){
            return this.$store.state.cars.lackNum;
        },
        cars(){
            return this.$store.state.cars.infomation;
        }
    },
    methods:{
        //获取社区名字
        getStreetNames(){
            this.$Axios.get(this.$url+'/human/getDropList_Community.do')
                        .then((response)=>{
                            this.streetNames=response.data.droplist;
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
        },
        getDrivers(){
            this.$Axios.get(this.$url+"/human/getVehicleInfo.do",{params:{
                            t: Date.parse(new Date())
                        }})
                        .then((response)=>{
                            // if(window.console){
                            //     console.log(response);
                            // }
                            if(response.data.list){
                                this.drivers=response.data.list;
                            }
                        })
                        .catch((error)=>{
                            if(window.console){
                                console.log(error);
                            }
                        })
        },
        //新增成员
        save(){
            if(this.username && this.age && this.driverAge && this.sex!="" && this.phone && this.community){
                $('#carModal').modal('hide');   
                this.$Axios.get(this.$url+'/human/editVehicleInfo.do',{
                    params:{
                        username:this.username,
                        age:this.age,
                        lon:this.driverAge,
                        phone:this.phone,
                        community:this.community,
                        sex:this.sex
                    }
                })
                .then((response)=>{
                    if(response.data.status){
                        this.msg=response.data.msg;
                        $("#tipsModal").modal("show");
                    }else{
                        this.msg=response.data.msg;
                        $("#tipsModal").modal("show");
                    }
                    this.getDrivers();
                    this.username="";this.age="";this.sex="";this.phone="";this.community="";this.driverAge="";
                })
                .catch((error)=>{
                    if(window.console){
                        console.log(error);
                    }
                })
            }else{
                this.msg="请填写完整的信息";
                $("#tipsModal").modal("show");
            }
        },
        //编辑成员信息
        editDriver(driver){
            this.editID=driver.id;
            this.editUserName=driver.username;
            this.editSex=driver.sex;
            if(this.editSex=="女"){
                this.editSex=0;
            }
            if(this.editSex=="男"){
                this.editSex=1;
            }
            this.editAge=driver.age;
            this.editDriverAge=driver.lon;
            this.editPhone=driver.phone;
            this.editCommunity=driver.community;
            $("#editCarModal").modal("show");
        },
        //保存编辑
        saveEditDriver(){
             if(this.editUserName && this.editAge &&  this.editDriverAge  && this.editPhone && this.editCommunity){
                $("#editCarModal").modal("hide");  
                this.$Axios.get(this.$url+'/human/editVehicleInfo.do',{
                    params:{
                        id:this.editID,
                        username:this.editUserName,
                        age:this.editAge,
                        phone:this.editPhone,
                        community:this.editCommunity,
                        sex:this.editSex,
                        lon:this.editDriverAge
                    }
                })
                .then((response)=>{
                    if(response.data.status){
                        this.getDrivers();
                    }else{
                        this.msg="编辑失败";
                        $("#tipsModal").modal("show");
                    }
                    
                })
                .catch((error)=>{
                   if(window.console){
                        console.log(error);
                    }
                })
            }else{
                this.msg="请填写完整的信息";
                $("#tipsModal").modal("show");
            }
        },
        //删除成员
        deleteDriver(driverID){
            this.$Axios.get(this.$url+'/human/delVehicleInfo.do ',{
                params:{
                    id:driverID
                }
            })
            .then((response)=>{
                if(response.data.status){
                    this.msg=response.data.msg;
                    $("#tipsModal").modal("show");
                    this.getDrivers();
                    
                }else{
                    this.msg=response.data.msg;
                    $("#tipsModal").modal("show");
                    this.getDrivers();
                }
            })
            .catch((error)=>{
                if(window.console){
                    console.log(error);
                }
            })           
        }
    }
}
</script>
<style lang="less" scoped>
#carHZ{
    height:100%;
    width: 100%;
    color: #666564;
    #carTitle{
        .box;
        background-color: #dadae2;
    }
    .box{
        width: 100%;
        height: 50px;
        padding-top: 15px;
        padding-left: 20px;
        font-weight: bold;
        border-bottom:1px solid #b1b0b0;
        padding-right: 20px;
        #refresh{
            float: right;
            &:hover{
                cursor: pointer;
            }
        }
    }
    table{
        width: 100%;
        text-align: center;
        caption{
            width: 100%;
            height: 50px;
            text-align: center;
            color:#666564;
            font-weight: bold;
            padding-top: 20px;
        }
        
        thead{
            background-color: #dadae2; 
            font-weight: bold;        
        }
        td{
            height: 50px;
            border:1px solid #b1b0b0;
            button{
                margin-right: 20px;
            }
        }
    }
    .modal-dialog{
        margin-top:7%;
    }

}
</style>




