<template>
    <div id="robotHZ">
        <div class="box">环卫机器人管理</div>
        <div class="box">核定环卫机器人编制数：{{num}}台</div>
        <div class="box"><span>现有：{{ownNum}}台</span><span style="margin-left:7%;">暂缺：{{lackNum}}台</span></div>
    </div>
</template>
<script>
export default {
    name:'robotHZ',
    computed:{
        num(){
            return this.$store.state.robots.num;
        },
        ownNum(){
            return this.$store.state.robots.ownNum;
        },
        lackNum(){
            return this.$store.state.robots.lackNum;
        }
    }

}
</script>
<style lang="less" scoped>
#robotHZ{
    height:100%;
    width: 100%;
    color: #666564;
    .box{
        width: 100%;
        height: 50px;
        padding-top: 15px;
        padding-left: 20px;
        font-weight: bold;
        border-bottom:1px solid #b1b0b0;;
    }
    table{
        width: 100%;
        text-align: center;
        caption{
            width: 100%;
            height: 50px;
            text-align: center;
            color:#666564;
            font-weight: bold;
            padding-top: 20px;
        }
        
        thead{
            background-color: #dadae2; 
            font-weight: bold;        
        }
        td{
            height: 50px;
            border:1px solid #b1b0b0;
        }
    }

}
</style>





