<template>
    <div id="environment">
        <div id="header">
            <img src="../assets/logo1.png" alt="logo">
            <ul>
                <li @click="changColor(0)"><router-link tag="div" :to="{name:'UsersManage'}" v-bind:style="{color:color[0]}"><span class="glyphicon glyphicon-user" aria-hidden="true"></span>用户管理</router-link></li>
                <li @click="changColor(1)"><router-link tag="div" :to="{name:'DeviceManage'}" v-bind:style="{color:color[1]}"><span class="glyphicon glyphicon-hdd" aria-hidden="true"></span>设备管理</router-link></li>
                <li @click="changColor(2)"><router-link tag="div" :to="{name:'Control'}" v-bind:style="{color:color[2]}"><span class="glyphicon glyphicon-th-large" aria-hidden="true"></span>控制台</router-link></li>
                <li @click="changColor(3)"><router-link tag="div" :to="{name:'SafeArea'}" v-bind:style="{color:color[3]}"><span class="glyphicon glyphicon-exclamation-sign" aria-hidden="true"></span>安全区域</router-link></li>
            </ul>
            <!-- <div id="userMsg">
                <div>您好，{{userName}}</div>
                <div>
                    <span class="msgCell">消息中心({{unread}}未读)</span>
                    <span class="msgCell"><span class="glyphicon glyphicon-cog" aria-hidden="true"></span>设置</span>
                    <span class="msgCell"><span class="glyphicon glyphicon-asterisk" aria-hidden="true"></span>关于</span>
                    <span class="msgCell">
                        <span class="glyphicon glyphicon-user" aria-hidden="true"></span>
                        <el-dropdown>
                            <span class="el-dropdown-link">个人中心</span>
                            <el-dropdown-menu slot="dropdown">
                                <el-dropdown-item>修改资料</el-dropdown-item>
                                <el-dropdown-item>修改密码</el-dropdown-item>
                                <el-dropdown-item>帮助</el-dropdown-item>
                                <el-dropdown-item>退出系统</el-dropdown-item>
                            </el-dropdown-menu>
                        </el-dropdown>
                    </span>
                </div>
            </div> -->
        </div>
        <div id="main">
           <router-view></router-view>
        </div>
    </div>
</template>
<script>
export default {
    name:"environment",
    data(){
        return{
            color:['#fd8403','','',''],
            userName:'teshineng (广东特视能智能科技有限公司)',
            unread:0
        }
    },
    methods:{
        changColor(n){
            this.color=['','','',''];
            this.color[n]="#fd8403";
        }
    }
}
</script>

<style lang="less" scoped>
#environment{
    height: 100%;
    width: 100%;
    #header{
        height:60px;
        width: 100%;
        // background-color: #040224;
        background-color: #282729;
        background-color: #1c2a4f;
        background-color: #1a2647;
        overflow: hidden;
        img{
            height: 90%;
            margin-top: 2px;
            margin-left: 10px;
            display: block;
            float: left;
            padding-right:20px;
        }
        ul{
            float: left;
            height: 100%;
            list-style-type: none;
            li{
                display: block;
                float: left;
                width: 150px;
                height: 100%;
                text-align: center;               
                div{
                    cursor: pointer;
                    // width: 70%;
                    // margin-left: 15%;
                    // height: 50%;
                    // margin-top:10%;
                    height: 100%;
                    width: 100%;
                    padding-top: 18px;
                    color: white;
                    font-size: 16px;
                    font-weight: bolder;
                    .glyphicon{
                        font-size: 15px;
                        margin-right: 5px;
                    }
                }
            }
        }
        #userMsg{
            padding-top:10px;
            padding-right: 15px;
            float:right;
            text-align: right;    
            color: white;
            .msgCell{
                padding-right:5px;
                cursor: pointer;
                .el-dropdown-link {
                    cursor: pointer;
                    color: white;
                }
            }
        }
    }
    #main{
        height:calc(100% - 60px);
        width: 100%;
    }
}
</style>





