<template>
  <div id="app">
      <Environment/>
      <!-- 客服 -->
      <div id="service" v-if="serviceShow"><img src="./assets/server.png" width="40" alt="客服" @click="showService"></div>
      <!-- 客服对话窗口 -->
      <div id="serviceContact" v-if="!serviceShow">
        <div style="width:100%;height:50px;backgroundColor:#1a2647;line-height:50px;color:aliceblue;"> 
            <img src="./assets/server1.png" alt="客服" height="70%" style="margin:0px 10px 0px 20px;">在线客服
            <span class="glyphicon glyphicon-remove" aria-hidden="true" style="float:right;margin:18px 20px;cursor: pointer;" @click="serviceShow=!serviceShow"></span>
        </div>
        <div id="display" style="width:100%;height:350px;backgroundColor:#ebeced;overflow:auto;padding-left:10px;">
          <!-- 客服的对话 -->
          <div style="padding-top:20px;">
            <div style="color:#afafaf;">系统 {{serverTime}}</div>
            <div><span style="padding:5px 10px;border-radius:20px;background-color:white;">尊敬的用户您好，请问有什么可以帮您？</span></div>       
          </div> 
          <!-- 发送的对话 -->
          <div v-for="message in messages" :key="message.id" style="text-align:right;padding-top:20px;padding-right:10px;">
            <div style="padding-right:10px;color:#afafaf;">{{message.sendTime}}</div>
            <div><span style="padding:5px 10px;border-radius:20px;background-color:#1a2647;color:white;">{{message.msg}}</span></div>       
          </div>   
        </div>
        <div id="enter" style="width:100%;height:100px;">
            <div style="width:100%;height:30px;">
              <img src="./assets/face.png" alt="表情" height="70%" style="margin:4px 0px 4px 15px;cursor: pointer;">
              <img src="./assets/file.png" alt="表情" height="65%" style="margin:4px 0px 4px 15px;cursor: pointer;">
            </div>
            <textarea  style="width:90%;height:65px;border:none;resize:none;outline:none;margin-left:5px;" placeholder="很高兴为您服务，请描述您的问题" v-model="sendMsg"></textarea>
            <button alarmName="button" class="btn" @click="send" style="margin-top:-60px;border:none;backgroundColor:#1a2647;color:white;">发送</button>
        </div>
      </div>
      <!-- 警告图标 -->
      <div id="warn"><span class="glyphicon glyphicon-bell" aria-hidden="true" @click="warn=!warn"></span></div>
      <div id="warnWindow" v-if="warn">
        <div id="warnHeader">
          <span class="glyphicon glyphicon-bell" aria-hidden="true" height="60%" style="margin:0px 10px 0px 10px;"></span>报警管理
            <span class="glyphicon glyphicon-remove" aria-hidden="true" style="float:right;margin-right:10px;cursor: pointer;" @click="warn=!warn"></span>
            <span class="glyphicon glyphicon-cog" aria-hidden="true" style="float:right;margin-right:10px;cursor: pointer;"></span>
        </div>
        <div id="tableContainer">
          <table>
            <thead>
              <tr><td>设备名称</td><td>IMEI</td><td>账户</td><td>警告类型</td><td>告警时间</td><td>定位状态</td><td>操作</td></tr>
            </thead>
            <tbody>
              <tr v-for="message in warnMessage" :key="message.id">
                <td>{{message.deviceName}}</td><td>{{message.imei}}</td><td>teshineng</td><td>{{message.alarmName}}</td><td>{{message.alarmTime}}</td><td>已定位</td><td style="color:#0087d6;"><div style="cursor:pointer;" @click="read(message.id,message.read_status)">{{message.read_status}}</div><div style="cursor:pointer;" @click="handle(message.id,message.handle_status)">{{message.handle_status}}</div></td>
              </tr>
            </tbody> 
          </table>
        </div>
        
      </div>
  </div>
</template>

<script>
import Qs from 'qs'
import Environment from '@/components/Environment'
export default {
  name: 'App',
  data(){
    return{
      serviceShow:true,
      serverTime:`${new Date().getHours().toString().padStart(2, '0')}:${new Date().getMinutes().toString().padStart(2, '0')}:${new Date().getSeconds().toString().padStart(2, '0')}`,
      sendMsg:'',
      messages:[],
      warn:false,    
      warnMessage:[],
      status:0,
      setTimeStatus:null
    }
  },
  components:{
    Environment
  },
  watch:{
    status(newValue){
      if(newValue=="1"){
        if(this.setTimeStatus==null){
          let status=true;
          this.setTimeStatus=setInterval(()=>{ 
            if(status){
              $("#warn").css("opacity","0");
              status=false;
            }else{
              $("#warn").css("opacity","1");
              status=true;
            }          
          },500)
        }
      }
      if(newValue=="0"){
        if(this.setTimeStatus!=null){
          clearInterval(this.setTimeStatus);
          $("#warn").css("opacity","1");
          this.setTimeStatus=null;
          
        }     
      }
    }
  },
  created(){
    this.getDevice();
    setInterval(()=>{
      this.getWarn();
    },1000)
  },
  mounted(){
      this.$router.push({name:'HuiZong'});//刷新后默认跳转到相应路由   
  },
  methods:{
    showService(){
      this.serviceShow=!this.serviceShow;
    },
    send(){
      if(this.sendMsg){
        let time=new Date();
        let sendTime= `${time.getHours().toString().padStart(2, '0')}:${time.getMinutes().toString().padStart(2, '0')}:${time.getSeconds().toString().padStart(2, '0')}`;
        this.messages.push({sendTime:sendTime,msg:this.sendMsg});
      }
    },
    //获取所有环卫工设备信息储存到store
    getDevice(){  
      this.$Axios.get(this.$url+'/human/getAllDeviceInfo.do')
                  .then((response)=>{                     
                      if(response.data.status){
                        this.$store.commit("saveDevices",response.data.list);                        
                      }                      
                  })
                  .catch((error)=>{
                      if(window.console){
                          console.log(error);
                      }
                  })
    },
    getWarn(){
      this.$Axios.get(this.$url+"/human/getPushDeviceAlarm.do",{params:{
                            t: Date.parse(new Date())
                        }})
                  .then((response)=>{
                    // if(window.console){
                    //   console.log(response);  
                    // }                    
                    if(response.data.list){
                      this.warnMessage=response.data.list;
                      this.status=0;
                      for(let i=0;i< this.warnMessage.length;i++){
                        if(this.warnMessage[i].read_status=="未读" || this.warnMessage[i].handle_status=="未处理"){
                          this.status=1;
                          break;
                        }
                      }
                    }
                  })
                  .catch((error)=>{
                    if(window.console){
                      console.log(error);
                    }
                  })
    },
    read(id,status){
      if(status=="未读"){
        this.$Axios.post(this.$url+"/human/updatePushDeviceAlarmStatus.do",Qs.stringify({
          id:id,
          flag:"read",
          t: Date.parse(new Date())
        })).catch((error)=>{
          if(window.console){
            console.log(error);
          }
        })
      }
    },
    handle(id,status){
      if(status=="未处理"){
        this.$Axios.post(this.$url+"/human/updatePushDeviceAlarmStatus.do",Qs.stringify({
          id:id,
          flag:"handle",
          t: Date.parse(new Date())
        })).catch((error)=>{
          if(window.console){
            console.log(error);
          }
        })
      }
    }
  }
}
</script>

<style lang="less">
*{
  padding:0px;
  margin: 0px;
  box-sizing: border-box;
}
html{
  height: 100%;
  width: 100%;
  font-size: 10px;
}
body{
  height: 100%;
  width: 100%;
}
.anchorBL{display:none;}
#app{
  height: 100%;
  width: 100%;
  // background-color: #040224;
  #service{
      position: absolute;
      z-index: 2;
      right: 20px;
      bottom:20px;
      cursor: pointer;
  }
  #serviceContact{
      position: absolute;
      z-index: 2;
      top:20%;
      left: 35%;
      width: 700px;
      height: 500px;
      background-color: white;
      border:1px solid #282729;
       border:1px solid #1a2647;
  }
  #warn{
    height: 47px;
    width: 40px;
    // background-color: #282729;
    color:#f34b4b;
    font-size: 40px;
    position: absolute;
    z-index: 2;
    top: 60%;
    right: 20px;
    span{
      cursor: pointer;
    }
  }
  #warnWindow{
    width: 1000px;
    height: 400px;
    border: 1px solid #b1b0b0;
    background-color: white;
    position: absolute;
    z-index: 2;
    bottom:5px;
    right: 0px;
    #warnHeader{
      width: 100%;
      height: 10%;
      background-color: #1a2647;
      color: white;
      padding-top:1%;
    }
    #tableContainer{
      width: 100%;
      height: 90%;
      overflow: auto;
      table{
        width: 100%;       
        color:#666564;
        text-align: center;
        thead{
          font-weight: bold;
          width: 100%;  
          // position: absolute;
        }
        tbody{
          width: 100%;
        }
        td{
          height: 30px;
          border-bottom:1px solid #dcdfe6;
          // width: 11%;
        }
           
      }
    }
    
  }
}
</style>



