<template>
    <div id="carCQ">
         <div class="box">环卫车辆出勤记录</div>
         <div class="box"><span>在岗车辆:{{onLine}}辆</span><span style="margin-left:30px;">处于维修中：{{onRest}}辆</span></div>
         <table>
            <caption>环卫车辆出勤情况</caption>
            <thead>           
                <tr>
                    <td>车辆号牌</td><td>签到时间</td><td>签退时间</td><td>状态</td>
                </tr>
            </thead>
            <tbody>
                <tr v-for="attend in attendance" v-bind:key="attend.id">
                    <td>{{attend.licenseNum}}</td><td>{{attend.start}}</td><td>{{attend.end}}</td><td>{{attend.status}}</td>
                </tr>
            </tbody>
        </table>
    </div> 
</template>
<script>
export default {
    name:'carCQ',
    data(){
        return{

        }
    },
    computed:{
        onLine(){
            return this.$store.state.cars.onLine;
        },
        onRest(){
             return this.$store.state.cars.onRest;
        },
        attendance(){
             return this.$store.state.cars.attendance;
        }
    }
}
</script>
<style lang="less" scoped>
#carCQ{
    width: 100%;
    height: 100%;
    color: #666564;
    .box{
        width: 100%;
        height: 50px;
        padding-top: 15px;
        padding-left: 20px;
        font-weight: bold;
        border-bottom:1px solid #b1b0b0;;
    }
    table{
        width: 100%;
        text-align: center;
        caption{
            width: 100%;
            height: 50px;
            text-align: center;
            color:#666564;
            font-weight: bold;
            padding-top: 20px;
        }
        
        thead{
            background-color: #dadae2; 
            font-weight: bold;        
        }
        td{
            height: 50px;
            border:1px solid #b1b0b0;
        }
    }
}
</style>




