import Vue from 'vue'
import Router from 'vue-router'
//一级
import UsersManage from '@/components/UsersManage'
//二级
import Worker from '@/components/UsersManageChildren/Worker'
//三级
import HuiZong from '@/components/UsersManageChildren/workerChildren/HuiZong'
import KaoQin from '@/components/UsersManageChildren/workerChildren/KaoQin'
import FenBu from '@/components/UsersManageChildren/workerChildren/FenBu'

//二级
import Car from '@/components/UsersManageChildren/Car'
//三级
import CarHZ from '@/components/UsersManageChildren/CarChildren/CarHZ'
import CarCQ from '@/components/UsersManageChildren/CarChildren/CarCQ'
import CarFB from '@/components/UsersManageChildren/CarChildren/CarFB'

//二级
import Robot from '@/components/UsersManageChildren/Robot'
//三级
import RobotHZ from '@/components/UsersManageChildren/RobotChildren/RobotHZ'
import RobotCQ from '@/components/UsersManageChildren/RobotChildren/RobotCQ'
import RobotFB from '@/components/UsersManageChildren/RobotChildren/RobotFB'



//一级
import DeviceManage from '@/components/DeviceManage' 
import Control from '@/components/Control'
import SafeArea from '@/components/SafeArea'
//二级
import Location from '@/components/ControlChildren/Location'
import Track from '@/components/ControlChildren/Track'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',name:'UsersManage',component:UsersManage,
      children:[
        {path:'/environment/Worker',name:'Worker',component:Worker,children:[{path:'/environment/HuiZong.do',name:'HuiZong',component:HuiZong},{path:'/environment/KaoQin',name:'KaoQin',component:KaoQin},{path:'/environment/FenBu',name:'FenBu',component:FenBu}],redirect:'/environment/HuiZong.do'},
        {path:'/environment/Car',name:'Car',component:Car,children:[{path:'/environment/CarHZ',name:'CarHZ',component:CarHZ},{path:'/environment/CarCQ',name:'CarCQ',component:CarCQ},{path:'/environment/CarFB',name:'CarFB',component:CarFB}],redirect:'/environment/CarHZ'},
        {path:'/environment/Robot',name:'Robot',component:Robot,children:[{path:'/environment/RobotHZ',name:'RobotHZ',component:RobotHZ},{path:'/environment/RobotCQ',name:'RobotCQ',component:RobotCQ},{path:'/environment/RobotFB',name:'RobotFB',component:RobotFB}],redirect:'/environment/RobotHZ'}
      ],
      redirect:'/environment/Worker'
    },
    {path:'/environment/DeviceManage',name:'DeviceManage',component:DeviceManage},
    {
      path:'/environment/Control',name:'Control',component:Control,
      children:[
        {path:'/environment/Location',name:'Location',component:Location},
        {path:'/environment/Track',name:'Track',component:Track}
      ],
      redirect:'/environment/Location'
    },
    {path:'/environment/SafeArea',name:'SafeArea',component:SafeArea},
    {path:'*',redirect:'/'}
  ],
  mode:'history'
})



