<template>
    <div id="worker">
        <!-- 显示菜单列表 -->
            <div id="nav">          
                <!-- <div id="workerTitle">环卫人员列表</div> -->
                <div class="box"><router-link tag="div" :to="{name:'HuiZong'}">人员管理</router-link></div>
                <div class="box"><router-link tag="div" :to="{name:'KaoQin'}">出勤记录</router-link></div>
                <!-- <div class="box"><router-link tag="div" :to="{name:'FenBu'}">人员管理</router-link></div> -->
            </div>     
            <div id="workerChild">
                <router-view></router-view> 
            </div>      
    </div>
</template>
<script>
export default {
    name:'worker',
    data(){
        return{
           
        }
    },
    methods:{
        
    }
}
</script>
<style lang="less" scoped>
#worker{
    width: 100%;
    height: 100%;
    color:aliceblue;
    #nav{
        width: 350px;
        height: calc(100% - 110px);
        position:absolute;
        left: 0px;
        // background-color: #040224;
        background-color: #282729;
        background-color: #1c2a4f;
        padding:20px 1px;
        #workerTitle{
            width: 100%;
            height: 50px;
            text-align: center;
            font-size: 15px;
            padding-top: 20px;
            font-weight: bold;
            color: #fd8403;
        }
        .box{
            width: 100%;
            height: 50px;
            text-align: center;   
            // border-radius: 5px;     
            &:hover{
                cursor: pointer;
                background-color: white;            
                color:#fd8403;
            }
            div{
                padding-top: 15px;
                width: 100%;
                height: 100%;
            }
        }
    }
    #workerChild{
        width:calc(100% - 350px);
        height: 100%;
        float:right;
        background-color: white;
        color: #666564;
    }
    
}

</style>





